/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getTimerTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { Container } from '../../utils/container';
import { makeText, msToTime } from '../../utils/index';

export class TimerView extends Container {
  constructor() {
    super();
    this._isFlashing = false;

    lego.event
      .on(ModelEvents.TimerModel.RemainingUpdate, this._onTimerUpdate, this)
      .on(ModelEvents.TimerModel.StoppedUpdate, this._onTimerStoppedUpdate, this);

    this._build();
    this._updateText(CI_API.Globals.PARAMS.game_timer);
  }

  get name() {
    return 'TimerView';
  }

  destroy() {
    lego.event
      .off(ModelEvents.TimerModel.RemainingUpdate, this._onTimerUpdate, this)
      .off(ModelEvents.TimerModel.StoppedUpdate, this._onTimerStoppedUpdate, this);

    super.destroy();
  }

  _updateText(value) {
    const time = msToTime(value);
    const text =
      parseInt(time.secondsStr, 10) >= 10
        ? `0:${parseInt(time.secondsStr, 10)}`
        : `0:0${parseInt(time.secondsStr, 10)}`;

    this._text.setText({
      text,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });
  }

  _onTimerStoppedUpdate(stopped) {
    if (stopped) {
      // this._hide();
    }
  }

  _onTimerUpdate(newValue) {
    this._updateText(newValue);
  }

  _build() {
    this._buildBg();
    this._buildText();
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x000000, 0.4);
    gr.drawRoundedRect(-50, -25, 100, 50, 20);
    gr.endFill();
    this.addChild((this._bg = gr));
  }

  _buildText() {
    this._text = makeText(getTimerTextConfig());
    this._text.anchor.set(0.5);
    this.addChild(this._text);
  }
}
