import { lego } from '@armathai/lego';
import { GameOverReasons } from '../../../constants';
import { showCtaCommand } from './show-cta-command';

export function scoreToCtaCompleteCommand() {
  const { cta_type: ctaType } = CI_API.Globals.PARAMS;
  lego.command
    //
    .payload(GameOverReasons.ScoreToCtaReached, ctaType === 'embedded' ? 0 : 1000)
    .execute(showCtaCommand);
}
