import { lego } from '@armathai/lego';
import { ctaModelGuard } from '../guards/ad/cta-model-guard';
import { hintModelGuard } from '../guards/ad/hint-model-guard';
import { joystickModelGuard } from '../guards/ad/joystick-model-guard';
import { persistentCtaModelGuard } from '../guards/ad/persistent-cta-model-guard';
import { playableIndicatorGuard } from '../guards/ad/playable-indicator-guard';
import { powerUpModelGuard } from '../guards/ad/power-up-model-guard';
import { soundParamGuard } from '../guards/ad/sound-param-guard';
import { gameModelGuard } from '../guards/game/game-model-guard';
import { playerModelGuard } from '../guards/player/player-model-guard';
import { timerModelGuard } from '../guards/timer/timer-model-guard';
import { destroyCtaModelCommand } from './ad/cta/destroy-cta-model-command';
import { destroyHintModelCommand } from './ad/hint/destroy-hint-model-command';
import { destroyJoystickModelCommand } from './ad/joystick/destroy-joystick-model-command';
import { destroyPersistentCtaModelCommand } from './ad/pcta/destroy-persistent-cta-model-command';
import { destroyPowerUpModelCommand } from './ad/power_up/destroy-power-up-model-command';
import { destroySoundModelCommand } from './ad/sound/destroy-sound-model-command';
import { destroyTimerModelCommand } from './ad/timer/destroy-timer-model-command';
import { stopTimerCommand } from './ad/timer/stop-timer-command';
import { destroyGameModelCommand } from './game/destroy-game-model-command';
import { destroyIndicatorModelCommand } from './indicator/destroy-indicator-model-command';
import { destroyPlayerModelCommand } from './player/destroy-player-model-command';

export function shutdownModelsCommand() {
  lego.command
    .guard(playerModelGuard)
    .execute(destroyPlayerModelCommand)

    .guard(timerModelGuard)
    .execute(stopTimerCommand, destroyTimerModelCommand)

    .guard(joystickModelGuard)
    .execute(destroyJoystickModelCommand)

    .guard(powerUpModelGuard)
    .execute(destroyPowerUpModelCommand)

    .guard(gameModelGuard)
    .execute(destroyGameModelCommand)

    .guard(ctaModelGuard)
    .execute(destroyCtaModelCommand)

    .guard(soundParamGuard)
    .execute(destroySoundModelCommand)

    .guard(hintModelGuard)
    .execute(destroyHintModelCommand)

    .guard(persistentCtaModelGuard)
    .execute(destroyPersistentCtaModelCommand)

    .guard(playableIndicatorGuard)
    .execute(destroyIndicatorModelCommand);
}
