const sparkle = 'sparkle';
const ctaConfetti = 'ctaConfetti';

export function getSparkleParticlesConfig() {
  return {
    key: sparkle,
    data: {
      lifespan: 600,
      frame: ['fx/fx_star.png'],
      scale: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      alpha: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      vy: { min: -2, max: 2 },
      vx: { min: -2, max: 2 },
      rotation: { delta: { min: -10, max: 10 } }
    }
  };
}

function getCTAWinConfettiParticlesConfig() {
  return {
    key: ctaConfetti,
    data: {
      lifespan: 2000,
      frame: [
        'confetti/01.png',
        'confetti/02.png',
        'confetti/03.png',
        'confetti/04.png',
        'confetti/05.png',
        'confetti/06.png'
      ],
      scale: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      alpha: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      vy: { min: -2, max: 2 },
      vx: { min: -2, max: 2 },
      rotation: { delta: { min: -10, max: 10 } }
    }
  };
}

export const particles = [getSparkleParticlesConfig(), getCTAWinConfettiParticlesConfig()];

export function getPCTASparkleEmitterConfig(parent, view) {
  const { x, y, width, height } = view;
  return {
    key: sparkle,
    parent,
    x: x - width / 2,
    y: y - height / 2,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createRectangleZone(width, height),
      total: 10,
      repeat: -1
    }
  };
}

export function getCTAWinConfettiEmitterConfig(parent, bounds) {
  const { x, y, width, height } = bounds;
  return {
    key: ctaConfetti,
    parent,
    x,
    y,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createRectangleZone(width, height),
      total: 20,
      repeat: -1
    }
  };
}
