/* eslint-disable no-restricted-properties */
/* eslint-disable class-methods-use-this */
import { getPagePeelAnimationConfig } from '../../configs/animation-configs';
import { makeAnimation } from '../../utils';
import { Container } from '../../utils/container';

export class PagePeelComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  getBounds() {
    return new Phaser.Rectangle(0, 0, 10, 10);
  }

  _getPeelPoints() {
    const vPoint = LP([0, -825], [0, -730]);
    const hPoint = LP([-775, 0], [-1170, 0]);

    return {
      hPoint,
      vPoint
    };
  }

  _getGlidePoints() {
    const vPoint = LP([0, -25], [0, -12.5]);
    const hPoint = LP([-25, 0], [-25, 0]);

    return {
      hPoint,
      vPoint
    };
  }

  _build() {
    this._pagePeel = makeAnimation(getPagePeelAnimationConfig());
    this._pagePeel.anchor.set(0.5, 1);

    this.addChild(this._pagePeel);

    const { vPoint, hPoint } = this._getPeelPoints();
    const { x: px, y: py } = this._pagePeel;

    const x = Math.abs(hPoint[0]);
    const y = Math.abs(vPoint[1]);

    const vertical = (Math.pow(x, 2) * y) / (Math.pow(x, 2) + Math.pow(y, 2));
    const horizontal = (Math.pow(y, 2) * x) / (Math.pow(x, 2) + Math.pow(y, 2));

    this._pagePeel.animations.play('open', 40, false);

    this.game.add
      .tween(this._pagePeel)
      .to({ x: px - horizontal, y: py - vertical }, 750, Phaser.Easing.Sinusoidal.InOut, true, 0)
      .onComplete.add(this._glide, this);

    this._pagePeel.angle = (-Math.atan(y / x) * 180) / Math.PI;
    // this._drawBounds();
  }

  _drawBounds() {
    const { x, y, width, height } = this.getBounds();
    const gr = this.game.add.graphics();
    gr.beginFill(0x0000ff);
    gr.drawRect(x, y, width, height);
    gr.endFill();

    this.addChild(gr);
  }

  _glide() {
    const { vPoint, hPoint } = this._getGlidePoints();
    const { x: px, y: py } = this._pagePeel;

    const x = Math.abs(hPoint[0]);
    const y = Math.abs(vPoint[1]);

    const vertical = (Math.pow(x, 2) * y) / (Math.pow(x, 2) + Math.pow(y, 2));
    const horizontal = (Math.pow(y, 2) * x) / (Math.pow(x, 2) + Math.pow(y, 2));
    this.game &&
      this.game.add
        .tween(this._pagePeel)
        .to({ x: px - horizontal, y: py - vertical }, 1000, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
  }
}
