/* eslint-disable class-methods-use-this */
import { getIndicatorFrameCircleConfig, getIndicatorLoadingCircleConfig } from '../../configs/image-configs';
import { makeImage } from '../../utils';
import { AbstractIndicator } from './abstract-indicator';

export class RoundIndicator extends AbstractIndicator {
  constructor(config) {
    super();

    this._build(config);
  }

  destroy() {
    this.game.tweens.remove(this._showTw);
    this.game.tweens.remove(this._hideTw);
    this.game.tweens.remove(this._circleTw);
    super.destroy();
  }

  show() {
    this._showTw = this.game.add.tween(this).to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);

    this._showTw.onComplete.add(() => {
      this.scaleIcon();
    });

    this._showTw.universal = true;
  }

  hide(force) {
    const duration = force ? 10 : 500;
    this._hideTw = this.game.add.tween(this).to({ alpha: 0 }, duration, Phaser.Easing.Sinusoidal.InOut, true, 0);
    this._hideTw.onComplete.add(() => {
      this.destroy();
    });
  }

  _build(config) {
    const { icon, text } = config;
    this.alpha = 0;
    this._buildBg();
    this._buildCircle();
    icon && this._buildIcon(icon);
    text && this._buildText(text);
  }

  _buildBg() {
    const bg = makeImage(getIndicatorFrameCircleConfig());
    this.addChild((this._bg = bg));
  }

  _buildCircle() {
    const circle = makeImage(getIndicatorLoadingCircleConfig());
    this._circleTw = this.game.add.tween(circle).to({ angle: 360 }, 1500, Phaser.Easing.Linear.None, true, 0, -1);
    this._circleTw.universal = true;

    this.addChild((this._circle = circle));
  }
}
