/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getSnakeDeathAnimationConfig } from '../../../configs/animation-configs';
import { getBoardBgTileImageConfig } from '../../../configs/image-configs';
import { getBoardBorderHorizontalLineNinePatchConfig } from '../../../configs/ninepatch-configs';
import { AdStatus, boardBorderOffset, boardDimensions, BoardState, FoodType } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { getGameBounds, makeAnimation, makeNinePatch, searchAtlasByFrame } from '../../../utils';
import { difference } from '../../../utils/array/difference';
import { Container } from '../../../utils/container';
import { randomInt } from '../../../utils/number/random-int';
import { randomReal } from '../../../utils/number/random-real';
import { FoodView } from './food-view';
import { SnakeView } from './snake-view';

function getPlayerLuck() {
  const random = randomReal(0, 1);
  let luck = false;

  if (store.ad.status === AdStatus.Cta) {
    luck = true;
    return luck;
  }

  switch (CI_API.Globals.PARAMS.difficulty) {
    case 'easy':
      luck = random > 0.3;
      break;
    case 'medium':
      luck = random > 0.5;
      break;
    case 'hard':
      luck = random > 0.7;
      break;
    default:
      break;
  }

  return luck;
}

export class BoardView extends Container {
  constructor() {
    super();

    this._playerSnake = null;
    this._snakes = [];
    this._foods = [];

    lego.event.on(ModelEvents.BoardModel.SnakesUpdate, this._onSnakesUpdate, this);
    lego.event.on(ModelEvents.BoardModel.FoodsUpdate, this._onFoodsUpdate, this);
    lego.event.on(ModelEvents.SnakeModel.ExhaustUpdate, this._onSnakeExhaustUpdate, this);
    lego.event.on(ModelEvents.SnakeModel.FragmentsUpdate, this._onSnakeFragmentsUpdate, this);

    this._build();
  }

  get name() {
    return 'BoardView';
  }

  get playerSnake() {
    return this._playerSnake;
  }

  get snakes() {
    return this._snakes;
  }

  get foods() {
    return this._foods;
  }

  getSnakeByUuid(uuid) {
    return this._snakes.find(s => s.uuid === uuid);
  }

  update() {
    super.update();
    const { status } = store.ad;
    const { cta_type: ctaType } = CI_API.Globals.PARAMS;

    if (this._playerSnake && (status === AdStatus.Game || (status === AdStatus.Cta && ctaType === 'embedded'))) {
      this._focusCameraOnPlayerSnake();
    }

    if (store.game.boardModel.state === BoardState.Paused) {
      return;
    }

    this._controlSnakesMovement();
    this._checkBordersCollision();
    this._checkCollisionsBetweenSnakes();
    status === AdStatus.Game && this._checkCollisionsWithFood();
  }

  _onSnakeExhaustUpdate(info) {
    const { uuid, skin } = info;
    const snake = this.getSnakeByUuid(uuid);
    const { tail } = snake;
    const { position, parent } = tail;

    const localPos = this.toLocal(position, parent);
    lego.event.emit(ViewEvents.BoardView.SnakeExhaust, { skin, position: localPos });
  }

  _onSnakeFragmentsUpdate(info) {
    const { uuid, skin, parts } = info;
    const snake = this.getSnakeByUuid(uuid);
    const fragmentsConfig = parts.map(p => {
      const part = snake.getPartByUuid(p.uuid);
      const {
        rotation,
        position,
        parent,
        config: { type }
      } = part;

      const { x, y } = position;
      const offset = randomReal(-10, 10);
      const localPos = this.toLocal(new Phaser.Point(x + offset, y + offset), parent);
      return { angle: rotation, position: localPos, skin, bodyPart: type, type: FoodType.Super };
    });
    lego.event.emit(ViewEvents.BoardView.FragmentsDrop, fragmentsConfig);
  }

  _checkCollisionsWithFood() {
    this._snakes.forEach(snake => {
      const { head } = snake;

      this._foods.forEach(food => {
        if (Phaser.Circle.intersects(head.collisionBody, food.collisionBody)) {
          lego.event.emit(ViewEvents.BoardView.SnakeEatFood, { snake: snake.uuid, food: food.uuid });
        }
      });
    });
  }

  _checkCollisionsBetweenSnakes() {
    this._snakes.forEach(currentSnake => {
      const { head } = currentSnake;

      this._snakes.forEach(snake => {
        if (currentSnake.uuid !== snake.uuid) {
          if (CI_API.Globals.PARAMS.damage_type === 'head_only') {
            if (Phaser.Circle.intersects(head.collisionBody, snake.head.collisionBody)) {
              lego.event.emit(ViewEvents.BoardView.SnakeBites, { biter: currentSnake.uuid, bitten: snake.uuid });
            }
          } else {
            snake.parts.forEach(p => {
              if (Phaser.Circle.intersects(head.collisionBody, p.collisionBody)) {
                lego.event.emit(ViewEvents.BoardView.SnakeBites, { biter: currentSnake.uuid, bitten: snake.uuid });
              }
            });
          }
        }
      });
    });
  }

  _checkBordersCollision() {
    this.snakes.forEach(snake => {
      const { uuid } = snake;
      const { width, height } = boardDimensions;
      const { collisionBody } = snake.head;
      const { x, y, diameter } = collisionBody;
      const { x: localX, y: localY } = this.toLocal(new Phaser.Point(x, y), null);

      if (
        localX + diameter / 2 > width ||
        localX - diameter / 2 < 0 ||
        localY + diameter / 2 > height ||
        localY - diameter / 2 < 0
      ) {
        lego.event.emit(ViewEvents.BoardView.SnakeCollideWithBorder, uuid);
      }
    });
  }

  _controlSnakesMovement() {
    const { status } = store.ad;
    this._snakes.forEach(snake => {
      if (this._playerSnake === snake && status === AdStatus.Game) {
        return;
      }

      this._avoidBorders(snake);
      this._avoidEnemies(snake);
      status === AdStatus.Game && this._targetFood(snake);
    });
  }

  _targetFood(snake) {
    const { detectorBody } = snake.head;

    for (let i = 0; i < this._foods.length; i += 1) {
      const food = this._foods[i];
      const { collisionBody } = food;

      if (Phaser.Circle.intersects(detectorBody, food.collisionBody)) {
        const rad = Phaser.Math.angleBetweenPoints(collisionBody, detectorBody);
        const newDirection = rad + Math.PI / 2;
        snake.setDirection(newDirection);
        break;
      }
    }
  }

  _avoidEnemies(snake) {
    const { detectorBody } = snake.head;
    const offset = randomInt(Math.PI / 4, Math.PI / 4);

    this._snakes.forEach(s => {
      if (s !== snake) {
        for (let i = 0; i < s.parts.length; i += 1) {
          const p = s.parts[i];
          const { collisionBody } = p;

          if (Phaser.Circle.intersects(detectorBody, collisionBody)) {
            const rad = Phaser.Math.angleBetweenPoints(collisionBody, detectorBody);
            const newDirection = rad - Math.PI + offset;
            getPlayerLuck() && snake.turnOffAutopilot();

            snake.autopilotOn && snake.setDirection(newDirection);
            break;
          }
        }
      }
    });
  }

  _avoidBorders(snake) {
    let newDirection = null;
    const offset = randomInt(-45, 45);
    const direction = snake.getDirection();
    const { width, height } = boardDimensions;
    const { detectorBody } = snake.head;
    const { x, y, diameter } = detectorBody;
    const { x: localX, y: localY } = this.toLocal(new Phaser.Point(x, y), null);

    if (localX + diameter / 2 > width) {
      if (Math.sin(direction) < 0) {
        newDirection = Phaser.Math.degToRad(90 + offset);
      }
    }
    if (localX - diameter / 2 < 0) {
      if (Math.sin(direction) > 0) {
        newDirection = Phaser.Math.degToRad(-90 + offset);
      }
    }
    if (localY + diameter / 2 > height) {
      if (Math.cos(direction) > 0) {
        newDirection = Phaser.Math.degToRad(-180 + offset);
      }
    }
    if (localY - diameter / 2 < 0) {
      if (Math.cos(direction) < 0) {
        newDirection = Phaser.Math.degToRad(0 + offset);
      }
    }

    if (newDirection) {
      snake.setDirection(newDirection);
    }
  }

  _focusCameraOnPlayerSnake() {
    const { width, height } = getGameBounds();
    const snakePos = this._playerSnake.getPosition();
    this.position.set(width / 2 - snakePos.x, height / 2 - snakePos.y);
  }

  _onFoodsUpdate(foods) {
    const views = this._foods.map(f => f.uuid);
    const models = foods.map(f => f.uuid);
    const add = difference(models, views);
    const remove = difference(views, models);
    add.length > 0 && this._addFoods(foods, add);
    remove.length > 0 && this._removeFoods(remove);
  }

  _addFoods(foods, diff) {
    this._foods.push(
      ...foods
        .filter(f => diff.includes(f.uuid))
        .map(f => {
          return this._makeFood(f);
        })
    );
  }

  _removeFoods(diff) {
    diff.forEach(d => {
      const index = this._foods.findIndex(f => f.uuid === d);
      const removedFoods = this._foods.splice(index, 1);

      const { type } = removedFoods[0].config;
      lego.event.emit(ViewEvents.BoardView.FoodEaten, type);

      removedFoods[0].destroy();
    });
  }

  _makeFood(foodModel) {
    const { uuid, config } = foodModel;
    const food = new FoodView(uuid, config);
    this.addChildAt(food, 1);

    return food;
  }

  _onSnakesUpdate(snakes) {
    const views = this._snakes.map(s => s.uuid);
    const models = snakes.map(s => s.uuid);
    const add = difference(models, views);
    const remove = difference(views, models);

    add.length > 0 && this._addSnakes(snakes, add);
    remove.length > 0 && this._removeSnakes(remove);
  }

  _addSnakes(snakes, diff) {
    this._snakes.push(
      ...snakes
        .filter(s => diff.includes(s.uuid))
        .map(s => {
          const snakeView = this._makeSnake(s);
          return this.addChild(snakeView);
        })
    );
  }

  _removeSnakes(diff) {
    diff.forEach(d => {
      const index = this._snakes.findIndex(s => s.uuid === d);
      const removedSnake = this._snakes.splice(index, 1);

      removedSnake[0].destroy();

      const { x, y } = removedSnake[0].getPosition();
      this._makeExplosion(x, y + 100);

      if (this._playerSnake && this._playerSnake.uuid === d) {
        this._playerSnake = null;
      }
    });
  }

  _makeSnake(snakeModel) {
    const { uuid, skin, parts, config, isPlayer } = snakeModel;
    const { initialDirection, initialPosition, name } = config;
    const { x, y } = initialPosition;

    const snake = new SnakeView(uuid, skin, parts, name);
    snake.setPosition(x, y);
    snake.setDirection(Phaser.Math.degToRad(initialDirection));

    if (isPlayer) {
      this._playerSnake = snake;
    }

    return snake;
  }

  _makeExplosion(x, y) {
    const explosion = makeAnimation(getSnakeDeathAnimationConfig());
    explosion.position.set(x, y);
    explosion.anchor.set(0.5, 1);
    explosion.animations.play('death', 30, false, true);
    this.addChild(explosion);
  }

  _build() {
    this._buildBg();
    this._buildBorders();
  }

  _buildBg() {
    const { width, height } = boardDimensions;
    const { frame } = getBoardBgTileImageConfig();
    const { key } = searchAtlasByFrame('ui/hand.png');
    const bg = new Phaser.TileSprite(this.game, 0, 0, width, height, key, frame);

    this.addChild(bg);
  }

  _buildBorders() {
    const { width, height } = boardDimensions;
    const top = makeNinePatch(getBoardBorderHorizontalLineNinePatchConfig());
    this.addChild(top);
    top.position.set(-boardBorderOffset, -boardBorderOffset);

    const bottom = makeNinePatch(getBoardBorderHorizontalLineNinePatchConfig());
    this.addChild(bottom);
    bottom.scale.set(1, -1);
    bottom.position.set(-boardBorderOffset, height + boardBorderOffset);

    const right = makeNinePatch(getBoardBorderHorizontalLineNinePatchConfig());
    this.addChild(right);
    right.angle = 90;
    right.position.set(width + boardBorderOffset, -boardBorderOffset);

    const left = makeNinePatch(getBoardBorderHorizontalLineNinePatchConfig());
    this.addChild(left);
    left.angle = -90;
    left.position.set(-boardBorderOffset, height + boardBorderOffset);
  }
}
