import { lego } from '@armathai/lego';
import { store } from '../../models/store';
import { setSnakeDeadCommand } from '../snake/set-snake-dead-command';

export function onSnakeBitesCommand(data) {
  const { biter, bitten } = data; // bitten
  const { boardModel } = store.game;
  const biterSnake = boardModel.getSnakeByUuid(biter);
  const bittenSnake = boardModel.getSnakeByUuid(bitten);

  let killed;

  if (CI_API.Globals.PARAMS.damage_type === 'head_only') {
    killed = bittenSnake.parts.length > biterSnake.parts.length ? biter : bitten;
  } else {
    killed = biter;
  }

  lego.command
    //
    .payload(killed)
    .execute(setSnakeDeadCommand);
}
