/* eslint-disable class-methods-use-this */

import { getPlayableIconConfig } from '../../configs/image-configs';
import { getIndicatorTextConfig } from '../../configs/text-configs';
import { makeImage, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class AbstractIndicator extends Container {
  // eslint-disable-next-line class-methods-use-this
  get name() {
    return 'indicator';
  }

  destroy() {
    this.game.tweens.remove(this._scaleIconTw);
    this.game.tweens.remove(this._shakeIconTw);
    super.destroy();
  }

  scaleIcon() {
    if (this._icon) {
      this._scaleIconTw = this.game.add
        .tween(this._icon.scale)
        .from({ x: 0, y: 0 }, 300, Phaser.Easing.Sinusoidal.InOut, true);
      this._scaleIconTw.onComplete.add(() => {
        this.shakingIcon();
      });
      this._scaleIconTw.onStart.add(() => {
        this._icon.alpha = 1;
      });
      this._scaleIconTw.universal = true;
    }
  }

  shakingIcon() {
    this._shakeIconTw = this.game.add
      .tween(this._icon)
      .to({ angle: [0, -20, 20, -20, 20, 0] }, 1000, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
    this._shakeIconTw.repeatDelay(300);
    this._shakeIconTw.universal = true;
  }

  _buildIcon(config) {
    const { x, y, scale } = config;
    const icon = makeImage(getPlayableIconConfig());
    scale && icon.scale.set(scale);
    icon.position.set(x, y);
    this.addChild((this._icon = icon));
    this._icon.alpha = 0;
  }

  _buildText(config) {
    const { wordWrap, wordWrapWidth, x, y, fontSize } = config;
    const text = makeText(getIndicatorTextConfig(wordWrap, wordWrapWidth, x, y, fontSize));
    text.anchor.set(0.5);

    this.addChild((this._text = text));
  }
}
