/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getTutorialSequenceGridConfig } from '../../configs/grid-configs';
import { getHandImageConfig } from '../../configs/image-configs';
import { getTutorialBgPatchConfig } from '../../configs/ninepatch-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import { fitText, getGameBounds, getViewByProperty, makeImage, makeNinePatch, makeText } from '../../utils';

export class TutorialSequenceView extends Phaser2Grid {
  constructor(config, index) {
    super(CI_API.game);

    this._showTween = null;
    this._hideTween = null;

    this._config = config;
    this._index = index;
    this.alpha = 0;

    this._build();
    this._hand && this._showHand();
  }

  getGridConfig() {
    return getTutorialSequenceGridConfig();
  }

  getHand() {
    return this._hand;
  }

  destroy() {
    this._showTween && this.game.tweens.remove(this._showTween);
    this._hideTween && this.game.tweens.remove(this._hideTween);

    this._hand && this.destroyHand();

    super.destroy();
  }

  destroyHand() {
    this._hand.destroy();
    this._hand = null;

    this._scaleTw && this.game.tweens.remove(this._scaleTw);
    this._handMoveTw && this.game.tweens.remove(this._handMoveTw);
    this._triggerMoveTw && this.game.tweens.remove(this._triggerMoveTw);
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._hand && this._rebuildHand();

    super.rebuild(config);
  }

  _rebuildHand() {
    this.destroyHand();
    this._buildHand();
    this._showHand();
  }

  show() {
    this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    return this._showTween;
  }

  hide() {
    this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    return this._hideTween;
  }

  _build() {
    super.build(this.getGridConfig());
    this._buildBg();
    this._buildLabel();

    this._hand && this.destroyHand();
    this._index === 0 && this._buildHand();
  }

  _buildBg() {
    this._bg = makeNinePatch(getTutorialBgPatchConfig());
    this.setChild(`${this._index}`, this._bg);
  }

  _buildLabel() {
    const { width, height } = this._bg;
    const { text } = this._config;
    const label = makeText(getTutorialTextConfig(text));

    label.anchor.set(0.5);
    fitText(label, width * 0.8, height * 0.8);

    this._bg.addChild((this._label = label));
  }

  _buildHand() {
    this._hand = makeImage(getHandImageConfig());
    this._hand.anchor.set(0.15, 0.25);
    this.addChild(this._hand);
  }

  _showHand() {
    let x;
    let y;

    if (CI_API.Globals.PARAMS.joystick_enabled) {
      const joystickView = getViewByProperty('name', 'JoystickView');
      const joystickGlobalPos = this.toGlobal(joystickView.position);
      const joystickLocalPos = this.toLocal(joystickGlobalPos, undefined);
      const { x: joystickX, y: joystickY } = joystickLocalPos;

      x = joystickX;
      y = joystickY;
    } else {
      const { width, height } = getGameBounds();

      x = width / 2;
      y = height / 2;
    }

    this._firstTimeShow(x, y);
  }

  _firstTimeShow(initialX, initialY) {
    this._hand.scale.set(1);
    this._hand.alpha = 1;
    this._hand.position.set(initialX, initialY);
    this._hand.angle = 0;
    this._hand.visible = true;
    const { x, y, angle } = this._hand;

    this.game.add
      .tween(this._hand)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 1000)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this._hand).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 1000);
  }

  _pointHand() {
    const signX = Phaser.Math.sign(this._hand.scale.x);
    const signY = Phaser.Math.sign(this._hand.scale.y);
    const toX = this._hand.scale.x - 0.1 * signX;
    const toY = this._hand.scale.y - 0.1 * signY;

    this._scaleTw = this.game.add.tween(this._hand.scale).to({ x: toX, y: toY }, 500, Phaser.Easing.Cubic.InOut, true);
    this._scaleTw.onComplete.add(this._moveHand, this);
    CI_API.Globals.PARAMS.joystick_enabled && this._scaleTw.onComplete.add(this._moveTrigger, this);
  }

  _moveHand() {
    this._handMoveTw = this.game.add
      .tween(this._hand)
      .to({ x: '-30', y: '-30' }, 1000, Phaser.Easing.Linear.None, true, 0, -1, true);
  }

  _moveTrigger() {
    const joystickView = getViewByProperty('name', 'JoystickView');
    const { trigger } = joystickView;

    this._triggerMoveTw = this.game.add
      .tween(trigger)
      .to({ x: '-30', y: '-30' }, 1000, Phaser.Easing.Linear.None, true, 0, -1, true);
  }
}
