import { delayRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';

export class IndicatorModel extends ObservableModel {
  constructor() {
    super('IndicatorModel');
    this._visible = false;

    this.makeObservable('_visible');
  }

  get visible() {
    return this._visible;
  }

  set visible(value) {
    this._visible = value;
  }

  initialize() {
    this._visible = true;
    this._startVisibilityTimer();
  }

  destroy() {
    this._stopVisibilityTimer();
    this._visible = false;
    super.destroy();
  }

  _startVisibilityTimer() {
    this._visibilityTimer = delayRunnable(CI_API.Globals.PARAMS.playable_indicator_duration, () => {
      this.destroy();
    });
  }

  _stopVisibilityTimer() {
    removeRunnable(this._visibilityTimer);
  }
}
