import { lego } from '@armathai/lego';
import { isPlayerSnakeGuard } from '../../guards/board/is-player-snake-guard';
import { updateScoreCommand } from '../player/update-score-command';

export function onSnakeEnergyUpdateCommand(energy, prevValue, uuid) {
  lego.command
    //
    .payload(uuid)
    .guard(isPlayerSnakeGuard)
    .execute(updateScoreCommand);
}
