/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { AdStatus, BoardState, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getViewByProperty } from '../../utils';
import { Container } from '../../utils/container';

export class JoystickView extends Container {
  constructor() {
    super();

    this._rad = 0;
    this._pointer = null;
    this._isPressed = false;
    this._build();

    this._bg.events.onInputDown.addOnce(this._onFirstClick, this);

    lego.event.on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this);
  }

  get name() {
    return 'JoystickView';
  }

  get pointer() {
    return this._pointer;
  }

  get trigger() {
    return this._trigger;
  }

  rebuild() {
    this._trigger && this._trigger.position.set(0, 0);
  }

  enable() {
    this._bg.inputEnabled = true;
    this._bg.input.priorityID = InputPriority.Game + 1;
    this._bg.events.onInputDown.add(this._onInputDown, this);

    this.game.input.onUp.add(this._onInputUp, this);
  }

  disable() {
    this._isPressed = false;
    this._trigger.position.set(0, 0);
    this._bg.inputEnabled = false;
    this._bg.events.onInputDown.remove(this._onInputDown, this);

    this.game.input.onUp.remove(this._onInputUp, this);
  }

  _onBoardStateUpdate(state) {
    switch (state) {
      case BoardState.Idle:
        this._bg.events.onInputDown.remove(this._onFirstClick, this);
        break;
      default:
        break;
    }
  }

  _onFirstClick() {
    lego.event.emit(ViewEvents.GameView.FirstClickOnController);
  }

  _onInputDown(target, pointer) {
    this._isPressed = true;
    this._pointer = pointer;

    lego.event.emit(ViewEvents.JoystickView.Pressed);
  }

  _onInputUp(pointer) {
    if (pointer === this._pointer) {
      this._isPressed = false;
      this._pointer = null;

      lego.event.emit(ViewEvents.JoystickView.Released);
    }
  }

  _build() {
    this._buildBg();
    this._buildTrigger();
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0xffffff, 0.5);
    gr.drawCircle(0, 0, 200);
    gr.endFill();

    gr.inputEnabled = true;
    gr.name = 'joystick';

    this.addChild((this._bg = gr));
  }

  _buildTrigger() {
    const gr = this.game.add.graphics();
    gr.beginFill(0xffffff, 1);
    gr.drawCircle(0, 0, 70);
    gr.endFill();

    this.addChild((this._trigger = gr));
  }

  update() {
    if (this._isPressed) {
      const { width } = this._bg;
      const { x: pointerX, y: pointerY } = this._pointer;
      const downPos = this.toLocal(new Phaser.Point(pointerX, pointerY), undefined);
      const { x: downX, y: downY } = downPos;
      const distance = Math.min(width / 2, Phaser.Math.distance(0, 0, downX, downY));

      const rad = Phaser.Math.angleBetweenPoints(new Phaser.Point(0, 0), new Phaser.Point(downX, downY));
      const x = Math.cos(rad) * distance;
      const y = Math.sin(rad) * distance;

      this._rad = rad;
      this._trigger.position.set(x, y);
    } else {
      this._trigger.position.set(0, 0);
    }

    const { status } = store.ad;
    const boardView = getViewByProperty('name', 'BoardView');
    if (this._isPressed && status === AdStatus.Game && boardView && boardView.playerSnake) {
      boardView.playerSnake.setDirection(this._rad - Math.PI / 2);
    }
  }
}
