import { isNarrowScreen, isSquareLikeScreen } from '../../utils';

export function getUIGridLandscapeConfig() {
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        padding: 0.1,
        bounds: { x: 0.7, y: 0.05, width: 0.3, height: 0.2 }
      },
      {
        name: 'joystick',
        bounds: { x: 0.7, y: 0.6, width: 0.3, height: 0.4 }
      },
      {
        name: 'power_up',
        bounds: { x: 0, y: 0.6, width: 0.3, height: 0.4 }
      },
      {
        name: 'timer',
        bounds: { x: 0.875, y: 0.4, width: 0.125, height: 0.15 }
      },
      {
        name: 'points',
        bounds: { x: 0, y: 0.075, width: 1, height: 0.15 }
      }
    ]
  };
}

export function getUIGridPortraitConfig() {
  const buttonsY = isSquareLikeScreen() ? 0.55 : 0.6;
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        bounds: { x: 0, y: 0.85, width: 1, height: 0.15 }
      },
      {
        name: 'joystick',
        bounds: { x: 0.6, y: buttonsY, width: 0.4, height: 0.2 }
      },
      {
        name: 'power_up',
        bounds: { x: 0, y: buttonsY, width: 0.4, height: 0.2 }
      },
      {
        name: 'timer',
        bounds: { x: 0.75, y: 0.25, width: 0.25, height: 0.2 }
      },
      {
        name: 'points',
        bounds: { x: 0, y: 0.075, width: 1, height: isNarrowScreen() ? 0.075 : 0.1 }
      }
    ]
  };
}
