import { CellScale } from '@armathai/phaser2-grid';

export function getBackgroundGridLandscapeConfig() {
  return {
    // debug: { color: 0xd95027 },
    name: 'background',
    cells: [
      {
        name: 'bg',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Envelop
      }
    ]
  };
}

