export const Animations = {
  PagePeel: {
    Open: 'open'
  },
  Snake: {
    Death: 'death'
  },
  Item: {
    Fall: 'fall'
  }
};

export function getPagePeelAnimationConfig(x, y) {
  return {
    x,
    y,
    data: [
      {
        name: Animations.PagePeel.Open,
        prefix: 'animation/peel/pagepeel_0',
        start: 1,
        stop: 7,
        suffix: '.png',
        zeroPad: 0,
        frameRate: 30,
        loop: false
      }
    ]
  };
}

export function getSnakeDeathAnimationConfig(x, y) {
  return {
    x,
    y,
    data: [
      {
        name: Animations.Snake.Death,
        prefix: 'animation/snake/snake_death_0',
        start: 1,
        stop: 8,
        suffix: '.png',
        zeroPad: 0,
        frameRate: 30,
        loop: false
      }
    ]
  };
}

export function getCoinFallAnimationConfig() {
  return {
    data: [
      {
        name: Animations.Item.Fall,
        prefix: 'animation/coins/coin_',
        start: 1,
        stop: 10,
        suffix: '.png',
        zeroPad: 0,
        frameRate: 40,
        loop: true
      }
    ]
  };
}
