/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getForegroundGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { postRunnable } from '../../utils';
import { IndicatorView } from '../indicator/indicator-view';
import { HintView } from './hint-view';
import { LogoView } from './logo-view';
import { SoundView } from './sound-view';
import { TutorialView } from './tutorial-view';

export class ForegroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.SoundModel.IconUpdate, this._onSoundIconUpdate, this)
      .on(ModelEvents.AdModel.HintUpdate, this._onHintUpdate, this)
      .on(ModelEvents.AdModel.TutorialUpdate, this._onTutorialUpdate, this)
      .on(ModelEvents.AdModel.PlayableIndicatorUpdate, this._onIndicatorUpdate, this)
      .on(ModelEvents.TutorialModel.CompleteUpdate, this._onTutorialCompleteUpdate, this)
      .on(ModelEvents.TutorialModel.SkipUpdate, this._onTutorialSkipUpdate, this);
  }

  get name() {
    return 'ForegroundView';
  }

  getGridConfig() {
    return getForegroundGridConfig();
  }

  _onAdStatusUpdate(status) {
    const { cta_type: ctaType } = CI_API.Globals.PARAMS;
    switch (status) {
      case AdStatus.Game:
        this._build();
        this._logoView && (this._logoView.alpha = 0);
        break;
      case AdStatus.Cta:
        this._buildLogo();

        if (ctaType === 'page_peel') {
          this._hideLogo();
        } else if (ctaType !== 'embedded') {
          this._showLogo();
        } else if (ctaType === 'embedded' && store.ad.cta.reason === GameOverReasons.Failed) {
          this._showLogo();
        }
        break;
      default:
    }
  }

  _onSoundIconUpdate(value) {
    value && this._buildSoundIcon();
  }

  _build() {
    //
  }

  // LOGO
  _buildLogo() {
    const { reason } = store.ad.cta;
    const cell = reason !== GameOverReasons.ScoreToCtaReached ? 'lose_cta_logo' : 'logo';
    this._logoView && this._logoView.destroy();
    this.setChild(cell, (this._logoView = new LogoView()));
    this._logoView.alpha = 0;
  }

  _showLogo() {
    this.game.add.tween(this._logoView).to({ alpha: 1 }, 500, Phaser.Easing.Cubic.InOut, true, 0);
  }

  _hideLogo() {
    this.game.add.tween(this._logoView).to({ alpha: 0 }, 500, Phaser.Easing.Cubic.InOut, true, 0);
  }

  // SOUND
  _buildSoundIcon() {
    this.setChild('sound', (this._soundView = new SoundView()));
  }

  // TUTORIAL
  _onTutorialUpdate(tutorial) {
    tutorial && this._buildTutorial();
  }

  _onTutorialCompleteUpdate(complete) {
    complete && this._hideTutorial().onComplete.add(this._destroyTutorial, this);
  }

  _onTutorialSkipUpdate(skip) {
    skip && this._destroyTutorial();
  }

  _buildTutorial() {
    this._tutorialView = new TutorialView();
    this._tutorialView.onSequenceReady.add(this._onTutorialSequenceReady, this);
  }

  _onTutorialSequenceReady() {
    this.setChild('tutorial', this._tutorialView);
  }

  _hideTutorial() {
    this._tutorialHideTw = this.game.add
      .tween(this._tutorialView)
      .to({ alpha: 0 }, 250, Phaser.Easing.Sinusoidal.InOut, true);
    this._tutorialHideTw.universal = true;

    this._tutorialHideTw.onComplete.add(() => {
      lego.event.emit(ViewEvents.TutorialView.HideComplete);
    });

    return this._tutorialHideTw;
  }

  _destroyTutorial() {
    this._tutorialHideTw && this.game.tweens.remove(this._tutorialHideTw);
    this._tutorialView.destroy();
    this._tutorialView = null;
  }

  // HINT
  _onHintUpdate(hint) {
    hint ? this._buildHint() : this._destroyHint();
  }

  _buildHint() {
    this.addChild((this._hintView = new HintView()));
  }

  _destroyHint() {
    this._hintView.destroy();
    this._hintView = null;
  }

  // INDICATOR
  _onIndicatorUpdate(value) {
    value ? this._buildIndicator() : this._destroyIndicator();
  }

  _buildIndicator() {
    this._indicatorView = new IndicatorView();
    this.setChild('indicator', this._indicatorView);
    postRunnable(() => {
      this._indicatorView.show();
    });
  }

  _destroyIndicator() {
    this._indicatorView.hide(store.ad.cta.preVisible);
  }
}
