import { getBoardConfig } from '../../../configs/board-config';
import { BoardState, SnakeState } from '../../../constants';
import { loopRunnable, removeRunnable } from '../../../utils';
import { difference } from '../../../utils/array/difference';
import { sample } from '../../../utils/array/sample';
import { shuffle } from '../../../utils/array/shuffle';
import { ObservableModel } from '../../observable-model';
import { FoodModel } from './food-model';
import { SnakeModel } from './snake/snake-model';

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');

    this._state = BoardState.Unknown;
    this._snakes = [];
    this._foods = null;
    this._playerSnake = null;
    this._foodProductionRunnable = null;

    this.makeObservable();
  }

  get playerSnake() {
    return this._snakes.find(s => s.isPlayer);
  }

  get enemies() {
    return this._snakes.filter(s => !s.isPlayer);
  }

  get snakes() {
    return this._snakes;
  }

  get foods() {
    return this._foods;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  getSnakeByUuid(uuid) {
    return this._snakes.find(s => s.uuid === uuid);
  }

  getFoodByUuid(uuid) {
    return this._foods.find(f => f.uuid === uuid);
  }

  // eslint-disable-next-line class-methods-use-this
  makeFragments(snake) {
    snake.makeFragments();
  }

  makeFood(data) {
    const food = new FoodModel(data);
    food.initialize();
    this._foods = [...this._foods, food];
  }

  removeSnake(uuid) {
    const snake = this.getSnakeByUuid(uuid);
    snake.destroy();
    this._snakes = difference(this._snakes, [snake]);
  }

  removeFood(uuid) {
    const food = this.getFoodByUuid(uuid);
    this._foods = difference(this._foods, [food]);
  }

  destroy() {
    removeRunnable(this._foodProductionRunnable);
  }

  initialize() {
    const { snakes, player } = sample(getBoardConfig());
    const count = CI_API.Globals.PARAMS.number_of_enemies;
    shuffle(snakes);
    this._snakes = this._createSnakes([player, ...snakes.slice(0, count)]);
    this._foods = this._createFoods();
    this._state = BoardState.Paused;
    this._startProduceFood();

    this._snakes.forEach(s => {
      s.state = SnakeState.Idle;
    });
  }

  _startProduceFood() {
    this._foodProductionRunnable = loopRunnable(2000, () => {
      const possibleCount = 100 - this._foods.length;
      if (possibleCount > 0) {
        this._foods = [...this._foods, ...this._createFoods(Math.min(5, possibleCount))];
      }
    });
  }

  // eslint-disable-next-line class-methods-use-this
  _createFoods(count = 50) {
    const foods = [];
    for (let i = 0; i < count; i += 1) {
      const food = new FoodModel();
      food.initialize();
      foods.push(food);
    }

    return foods;
  }

  // eslint-disable-next-line class-methods-use-this
  _createSnakes(snakesConfig) {
    return snakesConfig.map(config => {
      const snake = new SnakeModel(config);
      snake.initialize();

      return snake;
    });
  }
}
