import { ObservableModel } from '../../../observable-model';

export class SnakePartModel extends ObservableModel {
  constructor(config) {
    super('SnakeModelPart');

    const { type } = config;
    this._type = type;
    this._next = null;
    this._previous = null;
  }

  get type() {
    return this._type;
  }

  get next() {
    return this._next;
  }

  set next(value) {
    this._next = value;
  }

  get previous() {
    return this._previous;
  }

  set previous(value) {
    this._previous = value;
  }
}
