/* eslint-disable class-methods-use-this */
import { getCTA3PPGridConfig } from '../../configs/grid-configs';
import { postRunnable } from '../../utils';
import { CTAView } from './cta-view';

export class CTA3PPView extends CTAView {
  constructor() {
    super();

    this._build();
    this._show();
  }

  getGridConfig() {
    return getCTA3PPGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  _build() {
    this.build(this.getGridConfig());
    this.buildPopup('3PP_win');
  }

  _show() {
    postRunnable(this.showContent, this);
  }
}
