import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getBackgroundGridConfig } from '../../configs/grid-configs';
import { AdStatus } from '../../constants';
import { ModelEvents } from '../../events/model-events';

export class BackgroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onStatusUpdate, this);
  }

  get name() {
    return 'BackgroundView';
  }

  getGridConfig() {
    return getBackgroundGridConfig();
  }

  _onStatusUpdate(status) {
    switch (status) {
      case AdStatus.Game:
        // this._createBg('bg/game.jpg');
        break;
      default:
    }
  }
}
