import { CellScale } from '@armathai/phaser2-grid';

export function getCTAEmbeddedGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'popup',
        bounds: { x: 0.2, y: 0.6, width: 0.6, height: 0.4 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

export function getCTAEmbeddedGridPortraitConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'popup',
        bounds: { x: 0, y: 0.6, width: 1, height: 0.4 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}
