import { lego } from '@armathai/lego';
import { getPersistentCtaButtonConfig } from '../../configs/button-configs';
import { getPCTASparkleEmitterConfig } from '../../configs/particles-configs';
import { ViewEvents } from '../../events/view-events';
import { makeEmitter, pulse } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';

export class PersistentCTAView extends Container {
  constructor() {
    super();
    this._build();
  }

  getBounds() {
    const { width, height } = this._button;
    return new Phaser.Rectangle(-width / 2, -height / 2, width, height);
  }

  destroy() {
    this._sparkleEmitter && (this._sparkleEmitter.paused = true);
    this.game.tweens.removeFrom(this._button);
    this.game.tweens.removeFrom(this._button.scale);
    super.destroy();
  }

  _build() {
    this._buildButton();
    this._buildPersistentCtaEffect();
  }

  _buildPersistentCtaEffect() {
    const effect = CI_API.Globals.PARAMS.pcta_button_effect;
    switch (effect) {
      case 'sparkle':
        this._buildSparkle();
        break;
      case 'pulse':
        pulse(this);
        break;
      case 'both':
        this._buildSparkle();
        pulse(this);
        break;
      default:
        break;
    }
  }

  _buildButton() {
    this._button = new Button(getPersistentCtaButtonConfig());
    this._button.onClick.add(() => lego.event.emit(ViewEvents.PersistentCtaView.Click), this);
    this.addChild(this._button);
  }

  _buildSparkle() {
    const { x, y, width, height } = this;
    this._sparkleEmitter = makeEmitter(getPCTASparkleEmitterConfig(this, { x, y, width, height }));
    this.bringToTop(this._button);
  }
}
