import { getCTAPopupBgImageConfig } from '../../configs/image-configs';
import { getNewSkinBubblePatchConfig } from '../../configs/ninepatch-configs';
import { getSnakeSpineConfig, Spines } from '../../configs/spine-configs';
import { get3PPPopupTextConfig, getLosePopupTextConfig, getWinPopupTextConfig } from '../../configs/text-configs';
import { fitText, makeImage, makeNinePatch, makeSpine, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class CTAPopupView extends Container {
  constructor(type) {
    super();

    this._type = type;

    this._build();
  }

  _build() {
    switch (this._type) {
      case 'standard_win':
        this._buildStandardWinPopup();
        break;
      case '3PP_win':
        this._build3PPWinPopup();
        break;
      case 'lose':
        this._buildLosePopup();
        break;

      default:
        break;
    }
  }

  _buildStandardWinPopup() {
    this._bg = makeImage(getCTAPopupBgImageConfig());
    this._label = makeText(getWinPopupTextConfig());
    this._label.anchor.set(0.5);
    this._label.position.set(35, 0);

    const { width, height } = this._bg;
    fitText(this._label, width * 0.55, height * 0.7);

    this.addChild(this._bg);
    this.addChild(this._label);
  }

  _build3PPWinPopup() {
    this._bg = makeNinePatch(getNewSkinBubblePatchConfig());
    this._label = makeText(get3PPPopupTextConfig());
    this._label.anchor.set(0.5);
    this._label.position.set(0, -75);

    const { width, height } = this._bg;
    fitText(this._label, width * 0.6, height * 0.8);

    this.addChild(this._bg);
    this.addChild(this._label);

    this._buildSnake();
  }

  _buildLosePopup() {
    this._bg = makeImage(getCTAPopupBgImageConfig());
    this._label = makeText(getLosePopupTextConfig());
    this._label.anchor.set(0.5);
    this._label.position.set(35, 0);

    const { width, height } = this._bg;
    fitText(this._label, width * 0.55, height * 0.7);

    this.addChild(this._bg);
    this.addChild(this._label);
  }

  _buildSnake() {
    let skin;

    switch (CI_API.Globals.PARAMS.reward_skin) {
      case 'Dog':
        skin = 'dog';
        break;
      case 'StarDragon':
        skin = 'star_dragon';
        break;
      case 'Unicorn':
        skin = 'unicorn';
        break;
      default:
        break;
    }

    this._snakeContainer = new Container();
    this._snakeContainer.getBounds = () => {
      return new Phaser.Rectangle(-100, -50, 200, 100);
    };

    this._snake = makeSpine(getSnakeSpineConfig());
    this._snake.setSkinByName(skin);
    this._snake.setAnimationByName(0, Spines.Snake.Animations.Idle, true);

    this._snakeContainer.addChild(this._snake);
    this._snakeContainer.scale.set(1.3);
    this._snakeContainer.position.set(40, 25);

    this.addChild(this._snakeContainer);
  }
}
