import { EnemySpeed, PlayerSpeed } from '../../constants';
import { store } from '../../models/store';

export function resetSnakeSpeedCommand(snakeUuid) {
  const snake = store.game.boardModel.getSnakeByUuid(snakeUuid);
  const { isPlayer } = snake;

  snake.speed = isPlayer
    ? PlayerSpeed[CI_API.Globals.PARAMS.player_speed]
    : EnemySpeed[CI_API.Globals.PARAMS.enemy_snake_speed];
}
