import { lego } from '@armathai/lego';
import { boardIdleStateGuard } from '../../guards/board/board-idle-state-guard';
import { foodEatenGuard } from '../../guards/board/food-eaten-guard';
import { removeFoodCommand } from './remove-food-command';

export function onFoodStateUpdateCommand(state, prevState, uuid) {
  lego.command
    //
    .payload(uuid)
    .guard(boardIdleStateGuard, foodEatenGuard)
    .execute(removeFoodCommand);
}
