import { boardDimensions, FoodState, FoodType, SnakeSkin } from '../../../constants';
import { sample } from '../../../utils/array/sample';
import { randomReal } from '../../../utils/number/random-real';
import { objectKeys } from '../../../utils/object/object-keys';
import { ObservableModel } from '../../observable-model';

export class FoodModel extends ObservableModel {
  constructor(config = {}) {
    super('FoodModel');

    const { width, height } = boardDimensions;

    this._config = Object.assign(
      {
        skin: sample(objectKeys(SnakeSkin)),
        bodyPart: null,
        angle: 0,
        position: { x: randomReal(10, width - 10), y: randomReal(10, height - 10) },
        type: FoodType.Regular
      },
      config
    );

    this._state = FoodState.Unknown;
    this._type = this._config.type;

    this.makeObservable();
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get config() {
    return this._config;
  }

  get type() {
    return this._type;
  }

  initialize() {
    this._state = FoodState.Idle;
  }
}
