import { PowerUpState } from '../../constants';
import { ObservableModel } from '../observable-model';

export class PowerUpModel extends ObservableModel {
  constructor() {
    super('PowerUpModel');

    this._state = PowerUpState.Unknown;
    this.makeObservable();
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  destroy() {
    this._state = PowerUpState.Disable;

    super.destroy();
  }

  initialize() {
    this._state = PowerUpState.Disable;
  }
}
