/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getPagePeelCTAGridConfig } from '../../configs/grid-configs';
import { CtaBottomLayer } from './cta-bottom-layer';
import { PagePeelComponent } from './page-peel-component';

export class CTAPagePeel extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._build();
  }

  getGridConfig() {
    return getPagePeelCTAGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
    this._ctaBottomLayer && this._rebuildBottomLayer();
    this._pagePeel && this._rebuildPagePeel();
  }

  _rebuildBottomLayer() {
    this._ctaBottomLayer.destroy();
    this._buildBottomLayer();
  }

  _rebuildPagePeel() {
    this._pagePeel.destroy();
    this._buildPagePeel();
  }

  _build() {
    this.build(this.getGridConfig());

    this._buildBottomLayer();
    this._buildPagePeel();
  }

  _buildBottomLayer() {
    this._ctaBottomLayer = new CtaBottomLayer();
    this.setChild('bottom_layer', this._ctaBottomLayer);
  }

  _buildPagePeel() {
    this._pagePeel = new PagePeelComponent();
    this.setChild('content', this._pagePeel);
  }
}
