import { lego, not } from '@armathai/lego';
import { hintModelGuard } from '../../../guards/ad/hint-model-guard';
import { joystickStateEnableGuard } from '../../../guards/ad/joystick-state-enable-guard';
import { hasEnoughEnergyForPowerUpGuard } from '../../../guards/board/has-enough-energy-for-power-up-guard';
import { store } from '../../../models/store';
import { resetPowerUpCommand } from '../../snake/reset-power-up-command';
import { startHintVisibilityTimerCommand } from '../hint/start-hint-visibility-timer-command';
import { disablePowerUpCommand } from './disable-power-up-command';
import { enablePowerUpCommand } from './enable-power-up-command';

export function onPowerUpReleaseCommand() {
  lego.command
    //
    .payload(store.game.boardModel.playerSnake.uuid)
    .guard(hasEnoughEnergyForPowerUpGuard)
    .execute(enablePowerUpCommand)

    .payload(store.game.boardModel.playerSnake.uuid)
    .guard(not(hasEnoughEnergyForPowerUpGuard))
    .execute(disablePowerUpCommand)

    .payload(store.game.boardModel.playerSnake.uuid)
    .execute(resetPowerUpCommand)

    .guard(joystickStateEnableGuard, hintModelGuard)
    .execute(startHintVisibilityTimerCommand);
}
