import { lego } from '@armathai/lego';
import { BoardState } from '../../constants';
import { timerModelGuard } from '../../guards/timer/timer-model-guard';
import { startTimerCommand } from '../ad/timer/start-timer-command';
import { setBoardStateCommand } from '../board/set-board-state-command';

export function onFirstClickOnControllerCommand() {
  lego.command
    //
    .payload(BoardState.Idle)
    .execute(setBoardStateCommand)

    .guard(timerModelGuard)
    .execute(startTimerCommand);
}
