import { lego } from '@armathai/lego';
import { timerCompleteGuard } from '../../../guards/timer/timer-complete-guard';
import { timerToCtaCommand } from './timer-to-cta-command';

export function timerCompleteUpdateCommand(complete) {
  lego.command
    //
    .payload(complete)
    .guard(timerCompleteGuard)
    .execute(timerToCtaCommand);
}
