import { lego } from '@armathai/lego';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { setGamePointerCommand } from '../game/set-game-pointer-command';
import { userInteractionCommand } from './user-interaction-command';

export function onGameViewInputUp() {
  lego.command
    //
    .payload(false)
    .execute(setGamePointerCommand)

    .guard(hintModelGuard)
    .execute(startHintVisibilityTimerCommand)

    .execute(userInteractionCommand);
}
