import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getPagePeelCTAGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta_page_peel',
    cells: [
      {
        name: 'content',
        align: CellAlign.RightBottom,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'bottom_layer',
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'retry_button',
        bounds: { x: 0, y: 0.8, width: 1, height: 0.2 }
      },
      {
        name: 'play_button',
        bounds: { x: 0, y: 0.8, width: 1, height: 0.2 }
      },
      {
        name: 'middle_button',
        bounds: { x: 0, y: 0.8, width: 1, height: 0.2 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

