/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCtaPlayButtonConfig, getCtaRetryButtonConfig } from '../../configs/button-configs';
import { AdStatus, GameOverReasons, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import moduleBindings from '../../module-bindings';
import { makePixel, showFromBottom } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';
import { CTAPopupView } from './cta-popup-view';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
    this.bindings = moduleBindings;

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return new Error('function getGridConfig() is not implemented');
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  buildPlayButton() {
    this._playBtn = new Button(getCtaPlayButtonConfig());
    this._playBtn.onClick.add(this._onPlayClick, this);
    this._playBtn.alpha = 0;

    const { retries } = store.ad;
    const { reason } = store.ad.cta;

    const cell =
      // eslint-disable-next-line no-nested-ternary
      reason !== GameOverReasons.ScoreToCtaReached ? (retries === 0 ? 'middle_button' : 'play_button') : 'play_button';

    this.setChild(cell, this._playBtn);
  }

  buildRetryButton() {
    this._retryBtn = new Button(getCtaRetryButtonConfig());
    this._retryBtn.onClick.add(this._onRetryClick, this);
    this._retryBtn.alpha = 0;
    this.setChild('retry_button', this._retryBtn);
  }

  buildPopup(type) {
    this._popup = new CTAPopupView(type);
    this._popup.alpha = 0;
    this._popupContainer = new Container();
    this._popupContainer.addChild(this._popup);
    this.setChild('popup', this._popupContainer);
  }

  build(config) {
    const { retries } = store.ad;

    super.build(config);
    this._buildBlocker();

    this.buildPlayButton();
    retries > 0 && this.buildRetryButton();
  }

  showContent() {
    this.showPlayBtn();
    this._retryBtn && this.showRetryBtn();
    this.showBlocker();
    this.showPopup();
  }

  showPlayBtn() {
    const twArg = [this._playBtn, 500, 500, false, Phaser.Easing.Cubic.Out];
    const tw = showFromBottom(...twArg);

    tw.onStart.add(() => {
      this._playBtn.alpha = 1;
    });

    return tw;
  }

  showRetryBtn() {
    const twArg = [this._retryBtn, 500, 500, false, Phaser.Easing.Cubic.Out];
    const tw = showFromBottom(...twArg);

    tw.onStart.add(() => {
      this._retryBtn.alpha = 1;
    });

    return tw;
  }

  showBlocker() {
    const tw = this.game.add.tween(this._blocker).to({ alpha: 0.5 }, 500, Phaser.Easing.Cubic.InOut, true);
    tw.universal = true;

    return tw;
  }

  showPopup() {
    const tw = this.game.add.tween(this._popup).to({ alpha: 1 }, 500, Phaser.Easing.Cubic.InOut, true);
    tw.universal = true;

    return tw;
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this.build();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      default:
    }
  }

  _buildBlocker() {
    this._blocker = makePixel({ alpha: 0 });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = 'cta_overlay';
    this.setChild('blocker', this._blocker);

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this._onScreenClick, this);
    }
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
  }
}
