import { lego } from '@armathai/lego';
import { setFoodEatenCommand } from '../food/set-food-eaten-command';
import { updateSnakeEnergyCommand } from '../snake/update-snake-energy-command';

export function onSnakeEatFoodCommand(data) {
  const { snake, food } = data;

  lego.command
    //
    .payload(snake, food)
    .execute(updateSnakeEnergyCommand)

    .payload(food)
    .execute(setFoodEatenCommand);
}
