// TUTORIAL
export function getBoardBorderHorizontalLineNinePatchConfig() {
  const { width } = boardDimensions;
  return {
    width: width + 2 * boardBorderOffset,
    height: 32,
    frame: border,
    anchor: { x: 0, y: 0 }
  };
}

