/* eslint-disable class-methods-use-this */
import { getSnakePartImageConfig } from '../../../configs/image-configs';
import { FoodType, SkinColors, SnakeSkin } from '../../../constants';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class FoodView extends Container {
  constructor(uuid, config) {
    super();

    this._collisionBody = null;
    this._uuid = uuid;
    this._config = config;
    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get config() {
    return this._config;
  }

  get collisionBody() {
    return this._collisionBody;
  }

  update() {
    super.update();

    this.updateCollisionBodyPosition();
  }

  updateCollisionBodyPosition() {
    const { x, y } = this.toGlobal(this._content.position);
    this._collisionBody.x = x;
    this._collisionBody.y = y;
  }

  _build() {
    this._buildContent();
    this._buildCollisionBody();
  }

  _buildCollisionBody() {
    const { height } = this._content;
    this._collisionBody = new Phaser.Circle(0, 0, height);
  }

  _buildContent() {
    const { position, angle, type } = this._config;

    this._content = type === FoodType.Regular ? this._makeRegularFood() : this._makeSuperFood();
    this._content.position.copyFrom(position);
    this._content.rotation = angle;
    this.addChild(this._content);
  }

  _makeSuperFood() {
    const { skin, bodyPart } = this._config;

    const img = makeImage(getSnakePartImageConfig(SnakeSkin[skin], bodyPart));
    img.scale.set(0.8);

    return img;
  }

  _makeRegularFood() {
    const { skin } = this._config;

    const gr = this.game.add.graphics();
    gr.beginFill(SkinColors[skin], 1);
    gr.drawCircle(0, 0, 20);

    return gr;
  }
}
