import { lego, not } from '@armathai/lego';
import { boardUnknownStateGuard } from '../../guards/board/board-unknown-state-guard';
import { isPlayerSnakeGuard } from '../../guards/board/is-player-snake-guard';
import { snakeDeadGuard } from '../../guards/board/snake-dead-guard';
import { gameOverCommand } from '../game/game-over-command';
import { makeSnakeFragmentationCommand } from './make-snake-fragmentation-command';
import { removeSnakeCommand } from './remove-snake-command';

export function onSnakeStateUpdateCommand(state, prevState, uuid) {
  lego.command
    //
    .payload(uuid)
    .guard(not(boardUnknownStateGuard), snakeDeadGuard)
    .execute(makeSnakeFragmentationCommand)

    .payload(uuid)
    .guard(isPlayerSnakeGuard, snakeDeadGuard)
    .execute(gameOverCommand)

    .payload(uuid)
    .guard(not(boardUnknownStateGuard), snakeDeadGuard)
    .execute(removeSnakeCommand);
}
