/* eslint-disable class-methods-use-this */
import { getIndicatorFrameRectangleConfig } from '../../configs/image-configs';
import { makeImage } from '../../utils';
import { AbstractIndicator } from './abstract-indicator';

export class RoundedRectIndicator extends AbstractIndicator {
  constructor(config) {
    super();
    this._build(config);
  }

  destroy() {
    this.game.tweens.remove(this._showTw);
    this.game.tweens.remove(this._hideTw);
    super.destroy();
  }

  show() {
    const twX = this._getTwPosition();

    this._showTw = this.game.add.tween(this).from({ x: twX }, 1000, Phaser.Easing.Back.Out, true, 0);
    this._showTw.onStart.add(() => {
      this.alpha = 1;
    });
    this._showTw.onComplete.add(() => {
      this.scaleIcon();
    });
  }

  hide(force) {
    const duration = force ? 0 : 1000;
    const twX = this._getTwPosition();
    this._hideTw = this.game.add.tween(this).to({ x: twX }, duration, Phaser.Easing.Back.Out, true, 0);
    this._hideTw.onComplete.add(() => {
      this.destroy();
    });
  }

  _build(config) {
    const { icon, text } = config;
    this.alpha = 0;

    this._buildRoundedRect();
    icon && this._buildIcon(icon);
    text && this._buildText(text);
    this._position(icon, text);
  }

  _buildRoundedRect() {
    this.addChild((this._bg = this._buildRect()));
  }

  _buildRect() {
    const rect = makeImage(getIndicatorFrameRectangleConfig());
    return rect;
  }

  _position(icon, text) {
    if (icon && text) {
      switch (CI_API.Globals.PARAMS.playable_indicator_placement) {
        case 'bottom_right':
        case 'top_right':
          this._icon.centerX = this._bg.centerX - 90;
          this._text.left = this._icon.right + 18;
          this._icon.centerY = this._text.centerY;
          break;
        case 'bottom_left':
        case 'top_left':
          this._icon.centerX = this._bg.centerX;
          this._icon.left = this._text.right + 18;
          this._icon.centerY = this._text.centerY;
          break;
        default:
          break;
      }
    }
  }

  _getTwPosition() {
    let twX = 0;
    const { x: currentX } = this;
    switch (CI_API.Globals.PARAMS.playable_indicator_placement) {
      case 'bottom_right':
      case 'top_right':
        twX = currentX + 500;
        break;
      case 'bottom_left':
      case 'top_left':
        twX = currentX - 500;
        break;
      default:
        break;
    }

    return twX;
  }
}
