/* eslint-disable class-methods-use-this */
import { getSnakePartImageConfig } from '../../../configs/image-configs';
import { SnakePartType } from '../../../constants';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

const dimensions = Object.freeze({
  width: 4,
  height: 30,
  bodyRadius: 40
});

export class SnakePartComponent extends Container {
  constructor(config) {
    super();

    const { uuid } = config;
    this._uuid = uuid;
    this._config = config;
    this._collisionBody = null;
    this._spine = null;
    this._backNode = null;
    this._frontNode = null;

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get collisionBody() {
    return this._collisionBody;
  }

  get detectorBody() {
    return this._detectorBody;
  }

  get spine() {
    return this._spine;
  }

  get backNode() {
    return this._backNode.position;
  }

  get frontNode() {
    return this._frontNode.position;
  }

  get config() {
    return this._config;
  }

  get centralPosition() {
    const { width, height } = dimensions;
    return new Phaser.Point(width / 2, height / 2);
  }

  _build() {
    this._config.type === SnakePartType.Head && this._buildDetectorBody();
    this._buildCollisionBody();
    this._buildSkin();
    this._buildNodes();
    this._buildSpine();
  }

  _buildCollisionBody() {
    const { bodyRadius, height } = dimensions;
    const { type } = this._config;

    // ..............
    const gr = this.game.add.graphics(0, type === SnakePartType.Head ? height / 2 : -height / 2);
    gr.beginFill(0xff0000, 0);
    gr.drawCircle(0, 0, bodyRadius);
    this.addChild((this._debugBody = gr));
    // ..............

    this._collisionBody = new Phaser.Circle(0, 0, bodyRadius);
  }

  _buildDetectorBody() {
    const { bodyRadius, height } = dimensions;

    // ..............
    const gr = this.game.add.graphics(0, height / 2);
    gr.beginFill(0xff0000, 0);
    gr.drawCircle(0, 0, 3 * (bodyRadius / this.game.scale.scaleFactor.x));
    this.addChild((this._debugDetectorBody = gr));
    // ..............

    this._detectorBody = new Phaser.Circle(0, 0, 3 * (bodyRadius / this.game.scale.scaleFactor.x));
  }

  _buildSpine() {
    const { type } = this._config;
    const { width, height } = dimensions;
    const gr = this.game.add.graphics();
    gr.beginFill(0x0000ff, 0);
    gr.drawRect(-width / 2, 0, width, type === SnakePartType.Head ? height : -height);
    this.addChild((this._spine = gr));
  }

  _buildNodes() {
    const { height } = dimensions;
    const { type } = this._config;

    if (type === SnakePartType.Head) {
      const gr = this.game.add.graphics(0, 0);
      gr.beginFill(0xff0000, 0);
      gr.drawCircle(0, 0, 20);
      this.addChild((this._backNode = gr));

      const gr1 = this.game.add.graphics(0, height);
      gr1.beginFill(0x0000ff, 0);
      gr1.drawCircle(0, 0, 20);
      this.addChild((this._frontNode = gr1));
    } else {
      const gr = this.game.add.graphics(0, 0);
      gr.beginFill(0x0000ff, 0);
      gr.drawCircle(0, 0, 20);
      this.addChild((this._frontNode = gr));

      const gr1 = this.game.add.graphics(0, -height);
      gr1.beginFill(0xff0000, 0);
      gr1.drawCircle(0, 0, 20);
      this.addChild((this._backNode = gr1));
    }
  }

  _buildSkin() {
    const { height } = dimensions;
    const { skin, type } = this._config;
    const img = makeImage(getSnakePartImageConfig(skin, type));
    img.position.set(0, type === SnakePartType.Head ? height / 2 : -height / 2);
    this.addChildAt(img, 0);
  }
}
