/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getPowerUpGlowImageConfig, getPowerUpImageConfig } from '../../configs/image-configs';
import { AdStatus, GameOverReasons, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class PowerUpView extends Container {
  constructor() {
    super();

    this._isActive = false;
    this._build();

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getBounds() {
    return this._powerUp.getBounds();
  }

  enable() {
    this._isActive = true;
    this._powerUp.alpha = 1;
    this._powerUp.events.onInputDown.add(this._onInputDown, this);

    this._enableHighlight();
  }

  disable() {
    this._isActive = false;
    this._powerUp.alpha = 0.3;
    this._powerUp.events.onInputDown.remove(this._onInputDown, this);
    this._powerUp.events.onInputOut.remove(this._onInputOut, this);
    this._powerUp.events.onInputUp.remove(this._onInputUp, this);

    this._disableHighlight(true);
  }

  _enableHighlight() {
    if (!this._showGlowTw) {
      this._showGlowTw = this.game.add
        .tween(this._glow)
        .to({ alpha: 1 }, 1000, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
      this._showGlowTw.universal = true;
    }
  }

  _disableHighlight(force = false) {
    this._showGlowTw && this.game.tweens.remove(this._showGlowTw);
    this._showGlowTw && (this._showGlowTw = null);

    const duration = !force ? 1000 : 0;
    const tw = this.game.add.tween(this._glow).to({ alpha: 0 }, duration, Phaser.Easing.Sinusoidal.InOut, true, 0);

    return tw;
  }

  _onAdStatusUpdate(status) {
    const { reason } = store.ad.cta;
    const { playerSnake } = store.game.boardModel;
    const { ableToBoost } = playerSnake;

    switch (status) {
      case AdStatus.Cta:
        if (reason !== GameOverReasons.Failed && playerSnake && ableToBoost) {
          this._powerUp.alpha = 1;
          this._disableHighlight().onComplete.add(() => {
            this._enableHighlight();
          });
        } else {
          this._powerUp.alpha = 0.3;
          this._disableHighlight();
        }
        break;
      default:
        break;
    }
  }

  _onInputDown() {
    if (this._isActive) {
      lego.event.emit(ViewEvents.PowerUpView.Pressed);
      this._powerUp.events.onInputOut.add(this._onInputOut, this);
      this._powerUp.events.onInputUp.add(this._onInputUp, this);
    }
  }

  _onInputOut() {
    this._powerUp.events.onInputOut.remove(this._onInputOut, this);
    this._powerUp.events.onInputUp.remove(this._onInputUp, this);
    lego.event.emit(ViewEvents.PowerUpView.Released);
  }

  _onInputUp() {
    this._powerUp.events.onInputOut.remove(this._onInputOut, this);
    this._powerUp.events.onInputUp.remove(this._onInputUp, this);
    lego.event.emit(ViewEvents.PowerUpView.Released);
  }

  _build() {
    this._buildPowerUp();
    this._buildGlow();
  }

  _buildPowerUp() {
    this._powerUp = makeImage(getPowerUpImageConfig());
    this._powerUp.inputEnabled = true;
    this._powerUp.input.priorityID = InputPriority.Game + 1;
    this._powerUp.name = 'power_up';
    this._powerUp.scale.set(1.275);
    this.addChild(this._powerUp);
  }

  _buildGlow() {
    this._glow = makeImage(getPowerUpGlowImageConfig());
    this._glow.scale.set(1.275);
    this._glow.alpha = 0;
    this.addChild(this._glow);
  }
}
