import { getPointsTextConfig } from '../../configs/text-configs';
import { fitText, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class PointsComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  updatePoints(points) {
    this._text && this._text.destroy();
    this._buildPoints(points);
  }

  _build() {
    this._buildBg();
    this._buildPoints();
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x000000, 0.3);
    gr.drawRoundedRect(-100, -30, 200, 60, 20);
    gr.endFill();
    this.addChild((this._bg = gr));
  }

  _buildPoints(points = 0) {
    this._text = makeText(getPointsTextConfig(points));
    this._text.anchor.set(0.5);
    fitText(this._text, 200, 100);

    this.addChild(this._text);
  }
}
