import { FONT1, FONT2 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text) {
  return {
    text,
    style: {
      font: FONT1,
      fontSize: 38,
      fill: '#1d5063',
      align: 'center',
      wordWrap: true,
      wordWrapWidth: 450
    }
  };
}

// PERSISTENT
export function getPersistentCtaTextConfig() {
  return {
    text: 'cta_btn_persistent_text',
    y: 1,
    style: {
      font: FONT1,
      fontSize: 65,
      fill: '#ffffff',
      align: 'center',
      shadow: {
        x: 1,
        y: 2,
        color: '#d68800',
        blur: 4,
        shadowStroke: true,
        shadowFill: true
      }
    }
  };
}

