// INDICATOR
export function getIndicatorTextConfig(wordWrap, wordWrapWidth, x = 0, y = 0, fontSize = 23) {
  return {
    text: 'TRY PLAYING!',
    x,
    y,
    style: {
      font: FONT2,
      fontSize,
      fill: '#ffffff',
      align: 'center',
      lineSpacing: -10,
      wordWrap,
      wordWrapWidth
    }
  };
}

// TIMER
export function getTimerTextConfig(text) {
  return {
    text: {
      text: `${text}`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize: 28,
      fill: '#ffffff',
      align: 'center'
    }
  };
}

// POINTS
export function getPointsTextConfig(points = 0) {
  return {
    text: {
      text: `Points: ${points}`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize: 36,
      fill: '#ffffff',
      align: 'center',
      stroke: '#000000',
      strokeThickness: 2
    }
  };
}

