import { lego } from '@armathai/lego';
import { AdStatus, FoodType, JoystickState, PowerUpState, SnakeState } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

let sound;

function play(id, volume = 1) {
  sound.play(id, volume);
}

function stop(id) {
  sound.stop(id);
}

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }
  sound = soundLoader.addAudioSprite('audio');
}

function playGameTheme() {
  play('game-theme', 0.3);
}

function onAdStatusUpdate(status) {
  switch (status) {
    case AdStatus.Cta:
      break;
    default:
      break;
  }
}

function onFoodEat(data) {
  const { snake: snakeUuid, food: foodUuid } = data;
  const { boardModel } = store.game;
  const snake = boardModel.getSnakeByUuid(snakeUuid);
  const food = boardModel.getFoodByUuid(foodUuid);
  const { type } = food;

  if (snake.isPlayer) {
    switch (type) {
      case FoodType.Regular:
        play('pallet-eat');
        break;
      case FoodType.Super:
        play('super-pallet-eat');
        break;
      default:
        break;
    }
  }
}

function onPowerUpStateUpdate(state) {
  switch (state) {
    case PowerUpState.Pressed:
      play('button-tap');
      play('power-up', 1.5);
      break;
    case PowerUpState.Unknown:
    case PowerUpState.Disable:
    case PowerUpState.Enable:
      stop('power-up');
      break;
    default:
      break;
  }
}

function onJoystickStateUpdate(state) {
  switch (state) {
    case JoystickState.Pressed:
      play('button-tap');
      break;
    default:
      break;
  }
}

function onSnakeStateUpdate(state, prevState, uuid) {
  const snake = store.game.boardModel.getSnakeByUuid(uuid);

  switch (state) {
    case SnakeState.Idle:
      snake.isPlayer && play('player-spawn');
      break;
    case SnakeState.Dead:
      snake.isPlayer ? play('player-death', 0.5) : play('enemy-death', 0.5);
      break;
    default:
      break;
  }
}

function onButtonClick() {
  play('button-tap');
}

export function SoundObservant() {
  lego.event.once(ViewEvents.Game.UserInteraction, playGameTheme, this);
  lego.event
    .on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this)
    .on(ModelEvents.AdModel.StatusUpdate, onAdStatusUpdate, this)
    .on(ModelEvents.PowerUpModel.StateUpdate, onPowerUpStateUpdate, this)
    .on(ModelEvents.JoystickModel.StateUpdate, onJoystickStateUpdate, this)
    .on(ModelEvents.SnakeModel.StateUpdate, onSnakeStateUpdate, this)
    .on(ViewEvents.BoardView.SnakeEatFood, onFoodEat, this)
    .on(ViewEvents.PersistentCtaView.Click, onButtonClick, this)
    .on(ViewEvents.CtaView.PlayClick, onButtonClick, this)
    .on(ViewEvents.CtaView.RetryClick, onButtonClick, this);
}
