/* eslint-disable class-methods-use-this */
import { getCTAStandardLoseGridConfig } from '../../configs/grid-configs';
import { postRunnable } from '../../utils';
import { CTAView } from './cta-view';

export class CTALoseView extends CTAView {
  constructor() {
    super();

    this._build();
    this._show();
  }

  getGridConfig() {
    return getCTAStandardLoseGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  _build() {
    this.build(this.getGridConfig());
    this.buildPopup('lose');
  }

  _show() {
    postRunnable(this.showContent, this);
  }
}
