import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getCTA3PPGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'retry_button',
        padding: 0.05,
        align: CellAlign.CenterBottom,
        bounds: { x: 0, y: 0.55, width: 0.3, height: 0.2 }
      },
      {
        padding: 0.05,
        name: 'play_button',
        align: CellAlign.CenterTop,
        bounds: { x: 0, y: 0.75, width: 0.3, height: 0.25 }
      },
      {
        name: 'popup',
        padding: { x: 0, y: 0.05, width: 0.9, height: 0.9 },
        bounds: { x: 0.3, y: 0, width: 0.7, height: 1 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'confetti',
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

