import { JoystickState } from '../../constants';
import { ObservableModel } from '../observable-model';

export class JoystickModel extends ObservableModel {
  constructor() {
    super('JoystickModel');

    this._state = JoystickState.Unknown;
    this.makeObservable();
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  destroy() {
    this._state = JoystickState.Disable;

    super.destroy();
  }

  initialize() {
    this._state = JoystickState.Enable;
  }
}
