import { lego } from '@armathai/lego';
import { ModelEvents } from '../../events/model-events';
import { getGameBounds, getViewByProperty, searchAtlasByFrame } from '../../utils';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.15, 0.25);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    this._removeTw();
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    this._removeTw();
    let x;
    let y;

    if (CI_API.Globals.PARAMS.joystick_enabled) {
      const joystickView = getViewByProperty('name', 'JoystickView');
      const joystickGlobalPos = this.toGlobal(joystickView.position);
      const joystickLocalPos = this.toLocal(joystickGlobalPos, undefined);
      const { x: joystickX, y: joystickY } = joystickLocalPos;

      x = joystickX;
      y = joystickY;
    } else {
      const { width, height } = getGameBounds();

      x = width / 2;
      y = height / 2;
    }

    this._firstTimeShow(x, y);
  }

  _firstTimeShow(initialX, initialY) {
    this.scale.set(1);
    this.alpha = 1;
    this.position.set(initialX, initialY);
    this.angle = 0;
    this.visible = true;
    const { x, y, angle } = this;

    this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    const signX = Phaser.Math.sign(this.scale.x);
    const signY = Phaser.Math.sign(this.scale.y);
    const toX = this.scale.x - 0.1 * signX;
    const toY = this.scale.y - 0.1 * signY;

    this._scaleTw = this.game.add.tween(this.scale).to({ x: toX, y: toY }, 500, Phaser.Easing.Cubic.InOut, true);
    this._scaleTw.onComplete.add(this._moveHand, this);
    CI_API.Globals.PARAMS.joystick_enabled && this._scaleTw.onComplete.add(this._moveTrigger, this);
  }

  _moveHand() {
    this._handMoveTw = this.game.add
      .tween(this)
      .to({ x: '-30', y: '-30' }, 1000, Phaser.Easing.Linear.None, true, 0, -1, true);
  }

  _moveTrigger() {
    const joystickView = getViewByProperty('name', 'JoystickView');
    const { trigger } = joystickView;

    this._triggerMoveTw = this.game.add
      .tween(trigger)
      .to({ x: '-30', y: '-30' }, 1000, Phaser.Easing.Linear.None, true, 0, -1, true);
  }

  _hide() {
    this._removeTw();
    this.visible = false;
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);
    this.game.tweens.remove(this._triggerMoveTw);
    this.game.tweens.remove(this._handMoveTw);
    this.game.tweens.remove(this._scaleTw);
  }
}
