/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getSnakeNameTextConfig } from '../../../configs/text-configs';
import { BoardState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { store } from '../../../models/store';
import { delayRunnable, fitText, makeText, removeRunnable } from '../../../utils';
import { difference } from '../../../utils/array/difference';
import { last } from '../../../utils/array/last';
import { Container } from '../../../utils/container';
import { SnakePartComponent } from './snake-part-component';

export class SnakeView extends Container {
  constructor(uuid, skin, parts, name) {
    super();

    this._parts = null;
    this._path = [];
    this._skin = skin;
    this._uuid = uuid;
    this._autopilotOn = true;
    this._autopilotCooldownRunnable = null;

    lego.event.on(ModelEvents.SnakeModel.PartsUpdate, this._onPartsUpdate, this);

    this._build(skin, parts, name);
  }

  get uuid() {
    return this._uuid;
  }

  get autopilotOn() {
    return this._autopilotOn;
  }

  get head() {
    return this._parts[0];
  }

  get tail() {
    return last(this._parts);
  }

  get parts() {
    return this._parts;
  }

  getPosition() {
    return this.head.position;
  }

  setPosition(x, y) {
    this.head.position.set(x, y);
  }

  setDirection(rad) {
    this.head.rotation = rad;
  }

  getDirection() {
    return this.head.rotation;
  }

  getPartByUuid(uuid) {
    return this._parts.find(p => p.uuid === uuid);
  }

  turnOffAutopilot() {
    if (this._autopilotCooldownRunnable) {
      return;
    }

    this._autopilotOn = false;

    this._autopilotCooldownRunnable = delayRunnable(400, () => {
      removeRunnable(this._autopilotCooldownRunnable);
      this._autopilotCooldownRunnable = null;
      this._autopilotOn = true;
    });
  }

  destroy() {
    removeRunnable(this._autopilotCooldownRunnable);
    super.destroy();
  }

  update() {
    super.update();

    const { boardModel } = store.game;

    this._parts.forEach((part, index) => {
      const nextPart = this._parts[index + 1];
      if (nextPart) {
        this._updatePartAngle(part, nextPart);
        this._updatePartPosition(part, nextPart);
      }
      this._updatePartCollisionBodyPosition(part);
    });

    this._name.position.set(this.head.x, this.head.y - 30);

    if (boardModel.state === BoardState.Paused) {
      return;
    }

    const { speed } = store.game.boardModel.getSnakeByUuid(this._uuid);
    this.head.x += speed * Math.cos(this.head.rotation + Math.PI / 2);
    this.head.y += speed * Math.sin(this.head.rotation + Math.PI / 2);

    this._path.unshift(new Phaser.Point(this.head.x, this.head.y));
  }

  _onPartsUpdate(parts, prevValue, uuid) {
    if (this._uuid !== uuid) {
      return;
    }

    const views = this._parts.map(p => p.uuid);
    const models = parts.map(p => p.uuid);
    const add = difference(models, views);
    const remove = difference(views, models);

    add.length > 0 && this._addParts(parts, add);
    remove.length > 0 && this._removeParts(remove);

    this._updatePartsLayersOrder();
  }

  _addParts(parts, diff) {
    const newParts = parts
      .filter(p => diff.includes(p.uuid))
      .map(p => {
        const partView = this._makePart(p);
        this.addChild(partView);

        return partView;
      });
    this._parts.splice(this._parts.length - 1, 0, ...newParts);
  }

  _removeParts(diff) {
    diff.forEach(d => {
      const index = this._parts.findIndex(p => p.uuid === d);
      const removedParts = this._parts.splice(index, 1);

      removedParts[0].destroy();
    });
  }

  _updatePartsLayersOrder() {
    this._parts.forEach(p => this.addChildAt(p, 0));
  }

  _updatePartAngle(part, nextPart) {
    const { backNode: nextBackNode } = nextPart;
    const { backNode: currentBackNode } = part;

    const nextBackNodeLocal = this.toLocal(nextBackNode, nextPart);
    const currentBackNodeLocal = this.toLocal(currentBackNode, part);

    // @ts-ignore
    const angle = Phaser.Math.angleBetweenPoints(nextBackNodeLocal, currentBackNodeLocal);
    nextPart.rotation = angle - Math.PI / 2;
  }

  _updatePartPosition(part, nextPart) {
    const { backNode: currentBackNode } = part;
    const currentBackNodeLocal = this.toLocal(currentBackNode, part);

    nextPart.x = currentBackNodeLocal.x;
    nextPart.y = currentBackNodeLocal.y;
  }

  _updatePartCollisionBodyPosition(part) {
    const { centralPosition } = part;
    const { x, y } = part.toGlobal(centralPosition);

    part.collisionBody.x = x;
    part.collisionBody.y = y;

    if (part.detectorBody) {
      part.detectorBody.x = x;
      part.detectorBody.y = y;
    }
  }

  _build(skin, parts, name) {
    this._buildParts(parts);
    this._buildName(name);
  }

  _buildName(name) {
    this._name = makeText(getSnakeNameTextConfig(name));
    this._name.anchor.set(0.5);
    fitText(this._name, 150, 150);
    this.addChild(this._name);
  }

  _buildParts(parts) {
    this._parts = parts.map(partModel => {
      return this._makePart(partModel);
    });
  }

  _makePart(partModel) {
    const { type, uuid } = partModel;
    const part = new SnakePartComponent({ skin: this._skin, type, uuid });
    // part.y = -index * 50;
    this.addChildAt(part, 0);

    return part;
  }
}
