import { CellScale } from '@armathai/phaser2-grid';
import { getGameBounds, searchAtlasByFrame } from '../../utils';
import { getIndicatorFrameRectangleConfig, getIndicatorFrameTileConfig } from '../image-configs';

export function getIndicatorGridLandscapeConfig(type) {
  let offset = 0;
  switch (type) {
    case 'icon_text':
      offset = 60;
      break;
    case 'icon':
    case 'text':
      offset = 160;
      break;

    default:
      break;
  }
  const { height, width } = getGameBounds();
  const indicatorFrameTileTexture = searchAtlasByFrame(getIndicatorFrameTileConfig().frame);
  const indicatorFrameRectangleTexture = searchAtlasByFrame(getIndicatorFrameRectangleConfig().frame);
  const { sourceSizeH: indicatorFrameTileSourceSizeH } = CI_API.game.cache.getFrameByName(
    indicatorFrameTileTexture.key,
    indicatorFrameTileTexture.frame
  );
  const {
    sourceSizeW: indicatorFrameRectangleTextureSourceSizeW,
    sourceSizeH: indicatorFrameRectangleTextureSourceSizeH
  } = CI_API.game.cache.getFrameByName(indicatorFrameRectangleTexture.key, indicatorFrameRectangleTexture.frame);
  return {
    name: 'indicator',
    // debug: { color: 0x0ff0f0 },
    cells: [
      {
        name: 'top',
        bounds: { y: 0, x: 0, height: indicatorFrameTileSourceSizeH / height }
      },
      {
        name: 'top_bg',
        bounds: { y: 0, x: 0, height: indicatorFrameTileSourceSizeH / height },
        scale: CellScale.Fill
      },
      {
        name: 'top_left',
        bounds: {
          y: 0.09,
          x: 0,
          width: indicatorFrameRectangleTextureSourceSizeW / width,
          height: indicatorFrameRectangleTextureSourceSizeH / height
        },
        offset: { x: -offset }
      },
      {
        name: 'top_right',
        bounds: {
          y: 0.09,
          x: 1 - indicatorFrameRectangleTextureSourceSizeW / width,
          width: indicatorFrameRectangleTextureSourceSizeW / width,
          height: indicatorFrameRectangleTextureSourceSizeH / height
        },
        offset: { x: offset }
      },
      {
        name: 'bottom_bg',
        bounds: { y: 1 - indicatorFrameTileSourceSizeH / height, x: 0 },
        scale: CellScale.Fill
      },
      {
        name: 'bottom',
        bounds: { y: 1 - indicatorFrameTileSourceSizeH / height, x: 0 }
      },
      {
        name: 'bottom_right',
        bounds: {
          y: 0.76,
          x: 1 - indicatorFrameRectangleTextureSourceSizeW / width,
          width: indicatorFrameRectangleTextureSourceSizeW / width,
          height: indicatorFrameRectangleTextureSourceSizeH / height
        },
        offset: { x: offset }
      },
      {
        name: 'bottom_left',
        bounds: {
          x: 0,
          y: 0.76,
          width: indicatorFrameRectangleTextureSourceSizeW / width,
          height: indicatorFrameRectangleTextureSourceSizeH / height
        },
        offset: { x: -offset }
      },
      {
        name: 'middle',
        bounds: { y: 0.3, x: 0.35, width: 0.3, height: 0.4 }
      }
    ]
  };
}

