import { loopRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';

export class TimerModel extends ObservableModel {
  constructor(delta = CI_API.game.time.physicsElapsedMS) {
    super('TimerModel');

    this._complete = null;
    this._timer = null;
    this._remaining = null;
    this._duration = null;
    this._stopped = false;
    this._delta = delta;

    this.makeObservable();
  }

  get complete() {
    return this._complete;
  }

  get timer() {
    return this._timer;
  }

  get remaining() {
    return this._remaining;
  }

  get duration() {
    return this._duration;
  }

  get delta() {
    return this._delta;
  }

  get stopped() {
    return this._stopped;
  }

  set stopped(value) {
    this._stopped = value;
  }

  destroy() {
    this.stopTimer();
  }

  pause() {
    this.stopTimer();
  }

  resume() {
    this._startTimer();
  }

  startTimer(time) {
    this._duration = time;
    this._remaining = this._duration;
    this._startTimer();
  }

  stopTimer() {
    if (this._timer) {
      removeRunnable(this._timer);
    }
  }

  _startTimer() {
    this._timer = loopRunnable(this._delta, () => {
      this._remaining -= this._delta;
      if (this._remaining <= 0) {
        this._remaining = 0;
        this._complete = true;
        this.stopTimer();
      }
    });
  }
}
