import { EnemySpeed, PlayerSpeed, SnakePartType, SnakeSkin, SnakeState } from '../../../../constants';
import { loopRunnable, removeRunnable } from '../../../../utils';
import { ObservableModel } from '../../../observable-model';
import { SnakePartModel } from './snake-part-model';

function getSnakeSkinKey(skin) {
  let k;
  // eslint-disable-next-line no-restricted-syntax
  for (const key in SnakeSkin) {
    if (Object.hasOwnProperty.call(SnakeSkin, key)) {
      const element = SnakeSkin[key];
      if (element === skin) {
        k = key;
      }
    }
  }

  return k;
}

const defaultLength = 6;
const headConfig = { type: SnakePartType.Head };
const bodyConfig = { type: SnakePartType.Body };
const tailConfig = { type: SnakePartType.Tail };
const emptySnakeConfig = [headConfig, tailConfig];

export class SnakeModel extends ObservableModel {
  constructor(config) {
    super('SnakeModel');

    this._state = SnakeState.Unknown;
    this._config = config;
    this._isPlayer = config.isPlayer;
    this._parts = null;
    this._skin = null;
    this._ableToBoost = null;
    this._powerUp = null;
    this._exhaust = null;
    this._fragments = null;
    this._consumptionRunnable = null;
    this._speed = this._isPlayer
      ? PlayerSpeed[CI_API.Globals.PARAMS.player_speed]
      : EnemySpeed[CI_API.Globals.PARAMS.enemy_snake_speed];
    this._energy = 0;
    this.makeObservable();
  }

  get parts() {
    return this._parts;
  }

  get skin() {
    return this._skin;
  }

  get config() {
    return this._config;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get isPlayer() {
    return this._isPlayer;
  }

  get energy() {
    return this._energy;
  }

  get powerUp() {
    return this._powerUp;
  }

  set powerUp(value) {
    this._powerUp = value;
  }

  get speed() {
    return this._speed;
  }

  set speed(value) {
    this._speed = value;
  }

  get ableToBoost() {
    return this._ableToBoost;
  }

  get exhaust() {
    return this._exhaust;
  }

  get fragments() {
    return this._fragments;
  }

  destroy() {
    this.stopEnergyConsumption();
  }

  initialize() {
    const { skin } = this._config;
    this._skin = skin;

    this._initParts();
  }

  updateEnergy(energy) {
    this._energy = Math.max(0, this._energy + energy);

    if (this._energy > 15) {
      this._ableToBoost = true;
    } else {
      this._ableToBoost = false;
    }

    this._checkForGrow();
  }

  startEnergyConsumption() {
    this._consumptionRunnable = loopRunnable(300, this._consumeEnergy, this);
  }

  stopEnergyConsumption() {
    removeRunnable(this._consumptionRunnable);
  }

  makeFragments() {
    const parts = this._parts.slice(1, this._parts.length);
    this._fragments = { uuid: this._uuid, skin: getSnakeSkinKey(this._skin), parts };
  }

  _checkForGrow() {
    const delta = defaultLength + Math.floor(this._energy / 10) - this._parts.length;
    delta > 0 ? this._addParts(delta) : this._removeParts(Math.abs(delta));
  }

  _consumeEnergy() {
    this._exhaust = { uuid: this._uuid, skin: getSnakeSkinKey(this._skin) };
    this.updateEnergy(-1);
  }

  _addParts(count) {
    const parts = [];

    for (let i = 0; i < count; i += 1) {
      const part = new SnakePartModel(bodyConfig);
      part.previous = parts[i - 1] || null;

      if (parts[i - 1]) {
        parts[i - 1].next = part;
      }

      parts.push(part);
    }

    this._parts.splice(this._parts.length - 1, 0, ...parts);
    this._parts = [...this._parts];
  }

  _removeParts(count) {
    this._parts.splice(this._parts.length - 2, count);
    this._parts = [...this._parts];
  }

  _initParts() {
    this._initEmptySnake();
    this._addParts(defaultLength - this._parts.length);
  }

  _initEmptySnake() {
    const parts = [];

    emptySnakeConfig.forEach((p, i) => {
      const part = new SnakePartModel(p);

      part.previous = parts[i - 1] || null;

      if (parts[i - 1]) {
        parts[i - 1].next = part;
      }

      parts.push(part);
    });

    this._parts = parts;
  }
}
