/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons, JoystickState, PowerUpState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { hideToBottom, hideToRight, pulse, showFromBottom, showFromRight } from '../../utils';
import { JoystickView } from './joystick-view';
import { PersistentCTAView } from './persistent-cta-view';
import { PointsComponent } from './points-component';
import { PowerUpView } from './power-up-view';
import { TimerView } from './timer-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this)
      .on(ModelEvents.AdModel.PowerUpUpdate, this._onPowerUpUpdate, this)
      .on(ModelEvents.PowerUpModel.StateUpdate, this._onPowerUpStateUpdate, this)
      .on(ModelEvents.AdModel.JoystickUpdate, this._onJoystickUpdate, this)
      .on(ModelEvents.JoystickModel.StateUpdate, this._onJoystickStateUpdate, this)
      .on(ModelEvents.PlayerModel.ScoreUpdate, this._onPlayerScoreUpdate, this)
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  get name() {
    return 'UIView';
  }

  build(config) {
    super.build(config);
    this._buildPlayerPoints();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._joystick && this._rebuildJoystick();

    super.rebuild(config);
  }

  _rebuildJoystick() {
    this._joystick.rebuild();
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  _buildPlayerPoints() {
    this._points = new PointsComponent();
    this.setChild('points', this._points);
  }

  _showPoints() {
    this._points.alpha = 1;
  }

  _hidePoints() {
    this.game.add.tween(this._points).to({ alpha: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
  }

  // AD
  _onAdStatusUpdate(status) {
    const { cta_type: ctaType } = CI_API.Globals.PARAMS;
    switch (status) {
      case AdStatus.Game:
        this._showPoints();
        this._points.updatePoints(0);
        break;
      case AdStatus.Cta:
        if (ctaType !== 'embedded') {
          this._hidePoints();
        } else if (ctaType === 'embedded' && store.ad.cta.reason === GameOverReasons.Failed) {
          this._hidePoints();
        } else {
          pulse(this._points);
        }
        this._joystick && this._joystick.disable();
        break;
      default:
        break;
    }
  }

  // POINTS
  _onPlayerScoreUpdate(newScore) {
    this._points.updatePoints(newScore);
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    if (persistentCta) {
      this.game.tweens.remove(this._hideTw);
      this._persistentCtaView && this._destroyPersistentCta();
      this._buildPersistentCta();
      this._showPersistentCta();
    } else {
      this.game.tweens.remove(this._showTw);
      this._hidePersistentCta().onComplete.add(this._destroyPersistentCta, this);
    }
  }

  _buildPersistentCta() {
    this.setChild('p_cta', (this._persistentCtaView = new PersistentCTAView()));
  }

  _destroyPersistentCta() {
    this._persistentCtaView.destroy();
    this._persistentCtaView = null;
  }

  _showPersistentCta() {
    const twArg = [this._persistentCtaView, 0, 500, false, Phaser.Easing.Cubic.Out];
    this._showTw = LP(showFromRight, showFromBottom).call(null, ...twArg);
    return this._showTw;
  }

  _hidePersistentCta() {
    const twArg = [this._persistentCtaView, 0, 500, false, Phaser.Easing.Cubic.In];
    this._hideTw = LP(hideToRight, hideToBottom).call(null, ...twArg);
    return this._hideTw;
  }

  // POWER UP
  _onPowerUpUpdate(powerUp) {
    const { cta_type: ctaType } = CI_API.Globals.PARAMS;

    if (powerUp) {
      this._buildPowerUp();
    } else if (!powerUp && ctaType !== 'embedded') {
      this._hidePowerUp().onComplete.add(this._destroyPowerUp, this);
    } else if (!powerUp && ctaType === 'embedded' && store.ad.cta.reason === GameOverReasons.Failed) {
      this._hidePowerUp().onComplete.add(this._destroyPowerUp, this);
    }
  }

  _onPowerUpStateUpdate(state) {
    switch (state) {
      case PowerUpState.Enable:
        this._powerUp.enable();
        break;
      case PowerUpState.Disable:
        this._powerUp.disable();
        break;
      default:
        break;
    }
  }

  _buildPowerUp() {
    this._powerUp = new PowerUpView();
    this.setChild('power_up', this._powerUp);
  }

  _hidePowerUp() {
    const tw = this.game.add.tween(this._powerUp).to({ alpha: 0 }, 250, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;

    return tw;
  }

  _destroyPowerUp() {
    this._powerUp.destroy();
    this._powerUp = null;
  }

  // JOYSTICK
  _onJoystickUpdate(joystick) {
    const { cta_type: ctaType } = CI_API.Globals.PARAMS;

    if (joystick) {
      this._buildJoystick();
    } else if (!joystick && ctaType !== 'embedded') {
      this._hideJoystick().onComplete.add(this._destroyJoystick, this);
    } else if (!joystick && ctaType === 'embedded' && store.ad.cta.reason === GameOverReasons.Failed) {
      this._hideJoystick().onComplete.add(this._destroyJoystick, this);
    }
  }

  _onJoystickStateUpdate(state) {
    switch (state) {
      case JoystickState.Enable:
        this._joystick.disable();
        this._joystick.enable();
        break;
      case JoystickState.Disable:
        this._joystick.disable();
        break;
      default:
        break;
    }
  }

  _buildJoystick() {
    this._joystick = new JoystickView();
    this.setChild('joystick', this._joystick);
  }

  _hideJoystick() {
    const tw = this.game.add.tween(this._joystick).to({ alpha: 0 }, 250, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;

    return tw;
  }

  _destroyJoystick() {
    this._joystick.destroy();
    this._joystick = null;
  }

  // TIMER
  _onTimerUpdate(timer) {
    if (timer) {
      this._buildTimer();
      this._showTimer();
    } else {
      this._hideTimer().onComplete.add(this._destroyTimer, this);
    }
  }

  _buildTimer() {
    this._timerView && this._timerView.destroy();
    this._timerView = new TimerView();

    this.setChild('timer', this._timerView);
  }

  _showTimer() {
    const twArg = [this._timerView, 0, 500, false, Phaser.Easing.Cubic.Out];
    const hideTw = showFromRight(...twArg);
    return hideTw;
  }

  _hideTimer() {
    const twArg = [this._timerView, 0, 500, false, Phaser.Easing.Cubic.In];
    const showTw = hideToRight(...twArg);
    return showTw;
  }

  _destroyTimer() {
    this._timerView.destroy();
  }
}
