/* eslint-disable class-methods-use-this */
import { getGameBounds } from '../../utils';
import { AbstractIndicator } from './abstract-indicator';

export class RectIndicator extends AbstractIndicator {
  constructor(config) {
    super();
    this._build(config);
  }

  destroy() {
    this.game.tweens.remove(this._showTw);
    this.game.tweens.remove(this._hideTw);
    super.destroy();
  }

  show() {
    const y =
      CI_API.Globals.PARAMS.playable_indicator_placement === 'bottom' ? getGameBounds().height + this.height * 2 : 0;

    this._showTw = this.game.add.tween(this).from({ y }, 300, Phaser.Easing.Sinusoidal.InOut, true);

    this._showTw.onComplete.add(() => {
      this.scaleIcon();
    });
  }

  hide(force) {
    const duration = force ? 10 : 500;
    const y =
      CI_API.Globals.PARAMS.playable_indicator_placement === 'bottom'
        ? getGameBounds().height + this.height * 2
        : -this.height;
    this._hideTw = this.game.add.tween(this).to({ y }, duration, Phaser.Easing.Sinusoidal.InOut, true);
    this._hideTw.onComplete.add(() => {
      this.destroy();
    });
  }

  _build(config) {
    const { icon, text } = config;

    icon && this._buildIcon(icon);
    text && this._buildText(text);
    this._position(icon, text);
  }

  _position(icon, text) {
    if (icon && text) {
      this._text.left = this._icon.right + 18;
      this._text.centerY = this._icon.centerY + 2;
    }
  }
}
