import { lego } from '@armathai/lego';
import { powerUpModelGuard } from '../../guards/ad/power-up-model-guard';
import { isPlayerSnakeGuard } from '../../guards/board/is-player-snake-guard';
import { disablePowerUpCommand } from '../ad/power_up/disable-power-up-command';
import { enablePowerUpCommand } from '../ad/power_up/enable-power-up-command';
import { resetPowerUpCommand } from '../snake/reset-power-up-command';

export function onSnakeAbleToBoostUpdateCommand(ableToBoost, prevValue, uuid) {
  lego.command
    //
    .payload(uuid)
    .guard(powerUpModelGuard, isPlayerSnakeGuard, () => ableToBoost)
    .execute(enablePowerUpCommand)

    .payload(uuid)
    .guard(powerUpModelGuard, isPlayerSnakeGuard, () => !ableToBoost)
    .execute(disablePowerUpCommand)

    .payload(uuid)
    .execute(resetPowerUpCommand);
}
