import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAContainerGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { CTA3PPView } from './cta-3pp-view';
import { CTAEmbeddedView } from './cta-embedded-view';
import { CTALoseView } from './cta-lose-view';
import { CTAPagePeel } from './cta-page-peel';
import { CTAWinView } from './cta-win-view';

/* eslint-disable class-methods-use-this */
export class CTAContainer extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return getCTAContainerGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._build();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      case AdStatus.Retry:
        this._cta.destroy();
        this._cta = null;
        break;
      default:
    }
  }

  _build() {
    this.build(this.getGridConfig());
    this._buildCta();
  }

  _buildCta() {
    const { cta_type: type } = CI_API.Globals.PARAMS;

    switch (type) {
      case 'standard':
        this._buildStandardCta();
        break;
      case 'embedded':
        this._buildEmbeddedCta();
        break;
      case 'page_peel':
        this._buildPagePeelCta();
        break;
      case '3PP':
        this._build3PPCta();
        break;
      default:
        break;
    }
  }

  _buildStandardCta() {
    const { reason } = store.ad.cta;

    switch (reason) {
      case GameOverReasons.Idled:
      case GameOverReasons.Failed:
      case GameOverReasons.TimerToCta:
        this._cta = new CTALoseView();
        break;
      case GameOverReasons.ScoreToCtaReached:
        this._cta = new CTAWinView();
        break;
      default:
        break;
    }

    this.setChild('cta', this._cta);
  }

  _buildEmbeddedCta() {
    const { reason } = store.ad.cta;

    switch (reason) {
      case GameOverReasons.Failed:
        this._cta = new CTALoseView();
        break;
      case GameOverReasons.Idled:
      case GameOverReasons.TimerToCta:
      case GameOverReasons.ScoreToCtaReached:
        this._cta = new CTAEmbeddedView();
        break;
      default:
        break;
    }

    this.setChild('cta', this._cta);
  }

  _buildPagePeelCta() {
    this._cta = new CTAPagePeel();
    this.setChild('cta', this._cta);
  }

  _build3PPCta() {
    const { reason } = store.ad.cta;

    switch (reason) {
      case GameOverReasons.Idled:
      case GameOverReasons.Failed:
      case GameOverReasons.TimerToCta:
        this._cta = new CTALoseView();
        break;
      case GameOverReasons.ScoreToCtaReached:
        this._cta = new CTA3PPView();
        break;
      default:
        break;
    }

    this.setChild('cta', this._cta);
  }
}
