import { lego } from '@armathai/lego';
import { store } from '../../models/store';
import { setScoreCommand } from './set-score-command';

export function updateScoreCommand(snakeUuid) {
  const { game } = store;
  const { boardModel } = game;
  const { energy } = boardModel.getSnakeByUuid(snakeUuid);

  lego.command
    //
    .payload(energy * CI_API.Globals.PARAMS.score_multiplier)
    .execute(setScoreCommand);
}
