/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { AdStatus, BoardState, GameOverReasons, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { makePixel } from '../../utils';
import { BoardView } from './board/board-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._pointer = null;
    this._boardView = null;
    this._inputArea = null;

    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.GameModel.BoardModelUpdate, this._onBoardUpdate, this)
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  get pointer() {
    return this._pointer;
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  build(config) {
    super.build(config);
    this._buildInputArea();
  }

  _addListeners() {
    this._inputArea.events.onInputDown.addOnce(this._onFirstClick, this);
    if (!CI_API.Globals.PARAMS.joystick_enabled) {
      this._inputArea.events.onInputDown.addOnce(this._onDown, this);
      this._inputArea.events.onInputUp.add(this._onUp, this);
    }
  }

  _onFirstClick() {
    lego.event.emit(ViewEvents.GameView.FirstClickOnController);
  }

  _onUp() {
    this._pointer = null;
    this.game.input.deleteMoveCallback(this._onMove, this);
    this._inputArea.events.onInputDown.addOnce(this._onDown, this);
    lego.event.emit(ViewEvents.GameView.InputUp);

    this._destroyJoystick();
  }

  _onDown(target, pointer) {
    this._pointer = pointer;
    this.game.input.addMoveCallback(this._onMove, this);
    lego.event.emit(ViewEvents.GameView.InputDown);

    this._buildJoystick(pointer.x, pointer.y);
  }

  _onMove(pointer) {
    if (pointer === this._pointer && store.ad.status === AdStatus.Game) {
      const { width } = this._joystickBg;
      const { x: pointerX, y: pointerY } = pointer;
      const { x: downX, y: downY } = this._joystickBg;
      const distance = Math.min(width / 2, Phaser.Math.distance(downX, downY, pointerX, pointerY));

      const rad = Phaser.Math.angleBetweenPoints(new Phaser.Point(downX, downY), new Phaser.Point(pointerX, pointerY));
      const x = Math.cos(rad) * distance;
      const y = Math.sin(rad) * distance;

      this._rad = rad;
      this._joystickTrigger.position.set(downX + x, downY + y);

      if (this._boardView && this._boardView.playerSnake) {
        if (distance !== 0) {
          this._boardView.playerSnake.setDirection(this._rad - Math.PI / 2);
        }
      }
    }
  }

  // AD
  _onAdStatusUpdate(status) {
    const { cta_type: ctaType } = CI_API.Globals.PARAMS;
    switch (status) {
      case AdStatus.Game:
        this._addListeners();
        break;
      case AdStatus.PreCta:
        this._destroyJoystick();
        break;
      case AdStatus.Cta:
        if (ctaType === 'embedded' && store.ad.cta.reason !== GameOverReasons.Failed) {
          this._buildCTABlocker();
          this._showCTABlocker();
        }
        break;
      default:
        break;
    }
  }

  // CTA BLOCKER
  _buildCTABlocker() {
    this._ctaBlocker = makePixel({ alpha: 0 });
    this._ctaBlocker.inputEnabled = true;
    this._ctaBlocker.input.priorityID = InputPriority.Cta;
    this._ctaBlocker.name = 'cta_overlay';
    this._ctaBlocker.events.onInputDown.add(this._onCTABlockerClick, this);
    this.setChild('blocker', this._ctaBlocker);
  }

  _showCTABlocker() {
    const tw = this.game.add.tween(this._ctaBlocker).to({ alpha: 0.5 }, 500, Phaser.Easing.Cubic.InOut, true);
    tw.universal = true;

    return tw;
  }

  _onCTABlockerClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  // BOARD
  _onBoardUpdate(board) {
    board ? this._buildBoard() : this._destroyBoard();
  }

  _onBoardStateUpdate(state) {
    switch (state) {
      case BoardState.Idle:
        this._inputArea.events.onInputDown.remove(this._onFirstClick, this);
        break;
      default:
        break;
    }
  }

  _buildBoard() {
    this.addChild((this._boardView = new BoardView()));
  }

  _destroyBoard() {
    this._boardView.destroy();
  }

  _buildInputArea() {
    const inputArea = makePixel({ width: 1, height: 1, alpha: 0 });
    inputArea.inputEnabled = true;
    inputArea.name = 'background';
    this.setChild('input_area', (this._inputArea = inputArea));
  }

  _buildJoystick(x, y) {
    this._buildJoystickBg(x, y);
    this._buildJoystickTrigger(x, y);
  }

  _destroyJoystick() {
    this._joystickBg && this._joystickBg.destroy();
    this._joystickTrigger && this._joystickTrigger.destroy();
  }

  _buildJoystickBg(x, y) {
    const gr = this.game.add.graphics();
    gr.beginFill(0xffffff, 0);
    gr.drawCircle(0, 0, 200);
    gr.endFill();

    gr.position.set(x, y);

    this.addChild((this._joystickBg = gr));
  }

  _buildJoystickTrigger(x, y) {
    const gr = this.game.add.graphics();
    gr.beginFill(0xffffff, 0);
    gr.drawCircle(0, 0, 70);
    gr.endFill();

    gr.position.set(x, y);

    this.addChild((this._joystickTrigger = gr));
  }
}
