/* eslint-disable class-methods-use-this */

import { getCTABottomLayerGridConfig } from '../../configs/grid-configs';
import { getLogoImageConfig } from '../../configs/image-configs';
import { makeImage, makePixel } from '../../utils';
import { CTAView } from './cta-view';

export class CtaBottomLayer extends CTAView {
  constructor() {
    super();

    this._build();
    this._show();
  }

  getGridConfig() {
    return getCTABottomLayerGridConfig();
  }

  _getPeelPoints() {
    const vPoint = LP([0, -850], [0, -750]);
    const hPoint = LP([-800, 0], [-1200, 0]);

    return {
      hPoint,
      vPoint
    };
  }

  _getGlidePoints() {
    const vPoint = LP([0, -875], [0, -762.5]);
    const hPoint = LP([-825, 0], [-1225, 0]);

    return {
      hPoint,
      vPoint
    };
  }

  _show() {
    this.showBlocker();
    this._showPagePeel();
    this._showContent();
  }

  _showContent() {
    this.game.add.tween(this._logo).from({ alpha: 0 }, 500, Phaser.Easing.Cubic.InOut, true, 250, 0);
    this.game.add.tween(this._playBtn).to({ alpha: 1 }, 500, Phaser.Easing.Cubic.InOut, true, 250, 0);
    this._retryBtn &&
      this.game.add.tween(this._retryBtn).to({ alpha: 1 }, 500, Phaser.Easing.Cubic.InOut, true, 250, 0);
  }

  _build() {
    this.build(this.getGridConfig());

    this._buildSolidBg();
    this._buildMask();
    this._buildLogo();

    this.bringToTop(this._playBtn);
    this._retryBtn && this.bringToTop(this._retryBtn);
  }

  _buildLogo() {
    this._logo = makeImage(getLogoImageConfig());
    this.setChild('logo', this._logo);
  }

  _buildSolidBg() {
    this._bg = makePixel({ alpha: 1, tint: 0x27546b });
    this.setChild('content', this._bg);
  }

  _buildMask() {
    this._maskShape = this.game.add.graphics(0, 0);

    this._maskShape.beginFill(0x00ff00, 1);
    this._maskShape.drawPolygon(0, -25, 0, 0, -25, 0);

    this.mask = this._maskShape;
    this.setChild('mask', this._maskShape);
  }

  _showPagePeel() {
    this.visible = true;
    const { vPoint, hPoint } = this._getPeelPoints();

    const cPoint = [0, 0];
    this.game.add
      .tween(this._maskShape.currentPath.shape.points)
      .to([...vPoint, ...cPoint, ...hPoint], 750, Phaser.Easing.Sinusoidal.InOut, true, 0)
      .onComplete.add(this._glide, this);
  }

  _glide() {
    const cPoint = [0, 0];
    const { vPoint, hPoint } = this._getGlidePoints();

    this.game &&
      this.game.add
        .tween(this._maskShape.currentPath.shape.points)
        .to([...vPoint, ...cPoint, ...hPoint], 1000, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
  }
}
