/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getIndicatorGridConfig } from '../../configs/grid-configs';
import { getIndicatorFrameTileConfig } from '../../configs/image-configs';
import { IndicatorType } from '../../constants';
import { getGameBounds, makeImage } from '../../utils';
import { Container } from '../../utils/container';
import { getIndicatorConfigByParams } from './indicator-config';
import { RectIndicator } from './rect-indicator';
import { RoundIndicator } from './round-indicator';
import { RoundedRectIndicator } from './rounded-rect-indicator';

export class IndicatorView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._build();
  }

  getGridConfig() {
    const { playable_indicator_type: type } = CI_API.Globals.PARAMS;
    return getIndicatorGridConfig(type);
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }
    super.rebuild(config);
  }

  show() {
    this._indicator.show();

    if (this._bgContainer) {
      const y =
        CI_API.Globals.PARAMS.playable_indicator_placement === 'bottom'
          ? getGameBounds().height + this._indicator.height * 2
          : 0;

      this._showTw = this.game.add.tween(this._bgContainer).from({ y }, 300, Phaser.Easing.Sinusoidal.InOut, true);
    }
  }

  hide(force) {
    const duration = force ? 10 : 450;
    this._indicator.hide(force);

    if (this._bgContainer) {
      const y =
        CI_API.Globals.PARAMS.playable_indicator_placement === 'bottom'
          ? getGameBounds().height + this._indicator.height * 2
          : -this._indicator.height;

      this._hideTw = this.game.add.tween(this._bgContainer).to({ y }, duration, Phaser.Easing.Sinusoidal.InOut, true);
      this._hideTw.onComplete.add(() => {
        this._bgContainer.destroy();
      });
    }
  }

  _build() {
    const { playable_indicator_placement: place, playable_indicator_type: type } = CI_API.Globals.PARAMS;
    const config = getIndicatorConfigByParams(`${place}_${type}`);
    const { bg } = config;

    switch (bg.type) {
      case IndicatorType.Full:
        {
          this._bgContainer = new Container();
          const bgImage = makeImage(getIndicatorFrameTileConfig());
          bgImage.scale.y = place === 'top' ? 1 : -1;
          this._bgContainer.addChild(bgImage);
          this.setChild(`${place}_bg`, this._bgContainer);
          this.setChild(`${place}`, (this._indicator = new RectIndicator(config)));
        }
        break;
      case IndicatorType.Round:
        this.setChild(`${place}`, (this._indicator = new RoundIndicator(config)));
        break;
      case IndicatorType.RoundedRect:
        this.setChild(`${place}`, (this._indicator = new RoundedRectIndicator(config)));
        break;

      default:
        break;
    }
  }

  _onResize() {
    this.rebuild(this.getGridConfig());
  }
}
