import { lego } from '@armathai/lego';
import { boostSnakeSpeedCommand } from '../snake/boost-snake-speed-command';
import { resetSnakeSpeedCommand } from '../snake/reset-snake-speed-command';
import { startEnergyConsumptionCommand } from '../snake/start-energy-consumption-command';
import { stopEnergyConsumptionCommand } from '../snake/stop-energy-consumption-command';

export function onSnakePowerUpUpdateCommand(powerUp, prevValue, uuid) {
  lego.command
    //
    .payload(uuid)
    .guard(() => powerUp)
    .execute(boostSnakeSpeedCommand, startEnergyConsumptionCommand)

    .payload(uuid)
    .guard(() => !powerUp)
    .execute(resetSnakeSpeedCommand, stopEnergyConsumptionCommand);
}
