import { lego } from '@armathai/lego';
import { PowerUpState } from '../../../constants';
import { hintModelGuard } from '../../../guards/ad/hint-model-guard';
import { store } from '../../../models/store';
import { powerUpCommand } from '../../snake/power-up-command';
import { hideHintCommand } from '../hint/hide-hint-command';
import { stopHintVisibilityTimerCommand } from '../hint/stop-hint-visibility-timer-command';
import { setPowerUpStateCommand } from './set-power-up-state-command';

export function onPowerUpPressCommand() {
  lego.command
    //
    .payload(PowerUpState.Pressed)
    .execute(setPowerUpStateCommand)

    .payload(store.game.boardModel.playerSnake.uuid)
    .execute(powerUpCommand)

    .guard(hintModelGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand);
}
