/* eslint-disable class-methods-use-this */
import { getCTAStandardWinGridConfig } from '../../configs/grid-configs';
import { getCTAWinConfettiEmitterConfig } from '../../configs/particles-configs';
import { getGameBounds, makeEmitter, postRunnable } from '../../utils';
import { Container } from '../../utils/container';
import { CTAView } from './cta-view';

export class CTAWinView extends CTAView {
  constructor() {
    super();

    this._build();
    this._show();
  }

  getGridConfig() {
    return getCTAStandardWinGridConfig();
  }

  destroy() {
    this._confettiContainer && this._destroyConfetti();

    super.destroy();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
    this._confettiContainer && this._rebuildConfetti();
  }

  _rebuildConfetti() {
    this._destroyConfetti();
    this._buildConfetti();
  }

  _build() {
    this.build(this.getGridConfig());
    this.buildPopup('standard_win');
    this._buildConfetti();
  }

  _buildConfetti() {
    const { x, y, width, height } = getGameBounds();

    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(x, y, width, height);
    gr.endFill();

    this._confettiContainer = new Container();
    this._confettiContainer.addChild(gr);
    this.setChild('confetti', this._confettiContainer);

    this.bringToTop(this._popupContainer);
    this.bringToTop(this._playBtn);
    this._retryBtn && this.bringToTop(this._retryBtn);

    this._emitter = makeEmitter(
      getCTAWinConfettiEmitterConfig(this._confettiContainer, this._confettiContainer.getBounds())
    );
  }

  _destroyConfetti() {
    this._emitter.paused = true;
    this._confettiContainer.destroy();
    this._confettiContainer = null;
  }

  _show() {
    postRunnable(this.showContent, this);
  }
}
