import { lego, not } from '@armathai/lego';
import { JoystickState } from '../../../constants';
import { hintModelGuard } from '../../../guards/ad/hint-model-guard';
import { hintVisibleGuard } from '../../../guards/ad/hint-visible-guard';
import { hideHintCommand } from '../hint/hide-hint-command';
import { startHintVisibilityTimerCommand } from '../hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../hint/stop-hint-visibility-timer-command';

export function onJoystickStateUpdate(state) {
  switch (state) {
    case JoystickState.Pressed:
      lego.command.guard(hintModelGuard).execute(hideHintCommand, stopHintVisibilityTimerCommand);
      break;
    case JoystickState.Enable:
      lego.command.guard(hintModelGuard, not(hintVisibleGuard)).execute(startHintVisibilityTimerCommand);
      break;
    default:
      break;
  }
}
