import { lego } from '@armathai/lego';
import { JoystickState } from '../../../constants';
import { userInteractionCommand } from '../../view/user-interaction-command';
import { setJoystickStateCommand } from './set-joystick-state-command';

export function onJoystickReleasedCommand() {
  lego.command
    //
    .payload(JoystickState.Enable)
    .execute(setJoystickStateCommand)

    .execute(userInteractionCommand);
}
