import { lego, not } from '@armathai/lego';
import { hintModelGuard } from '../../../guards/ad/hint-model-guard';
import { hintParamGuard } from '../../../guards/ad/hint-param-guard';
import { joystickModelGuard } from '../../../guards/ad/joystick-model-guard';
import { joystickStatePressedGuard } from '../../../guards/ad/joystick-state-pressed-guard';
import { persistentCtaParamGuard } from '../../../guards/ad/persistent-cta-param-guard';
import { gamePointerExistGuard } from '../../../guards/game/game-pointer-exist-guard';
import { initializeHintModelCommand } from '../hint/initialize-hint-model-command';
import { startHintVisibilityTimerCommand } from '../hint/start-hint-visibility-timer-command';
import { initializePersistentCtaModelCommand } from '../pcta/initialize-persistent-cta-model-command';

export function onTutorialCompleteCommand() {
  lego.command
    //
    .guard(persistentCtaParamGuard)
    .execute(initializePersistentCtaModelCommand)

    .guard(not(hintModelGuard), hintParamGuard)
    .execute(initializeHintModelCommand)

    .guard(joystickModelGuard, not(joystickStatePressedGuard))
    .execute(startHintVisibilityTimerCommand)

    .guard(not(joystickModelGuard), not(gamePointerExistGuard))
    .execute(startHintVisibilityTimerCommand);
}
