import { AdStatus, AdViewState } from '../../constants';
import { ObservableModel } from '../observable-model';
import { CtaModel } from './cta-model';
import { HintModel } from './hint-model';
import { IndicatorModel } from './indicator-model';
import { JoystickModel } from './joystick-model';
import { LoadModel } from './load-model';
import { PersistentCtaModel } from './persistent-cta-model';
import { PowerUpModel } from './power-up-model';
import { SoundModel } from './sound-model';
import { TimerModel } from './timer-model';
import { TutorialModel } from './tutorial-model';

export class AdModel extends ObservableModel {
  constructor() {
    super('AdModel');

    this._cta = null;
    this._hint = null;
    this._load = null;
    this._sound = null;
    this._timer = null;
    this._powerUp = null;
    this._joystick = null;
    this._tutorial = null;
    this._persistentCta = null;
    this._playableIndicator = null;
    this._status = AdStatus.Unknown;
    this._viewState = AdViewState.Unknown;
    this._retries = CI_API.Globals.PARAMS.retries;

    this.makeObservable();
  }

  get status() {
    return this._status;
  }

  set status(value) {
    this._status = value;
  }

  get viewState() {
    return this._viewState;
  }

  set viewState(value) {
    this._viewState = value;
  }

  get load() {
    return this._load;
  }

  get hint() {
    return this._hint;
  }

  get timer() {
    return this._timer;
  }

  get powerUp() {
    return this._powerUp;
  }

  get joystick() {
    return this._joystick;
  }

  get tutorial() {
    return this._tutorial;
  }

  get persistentCta() {
    return this._persistentCta;
  }

  get playableIndicator() {
    return this._playableIndicator;
  }

  get cta() {
    return this._cta;
  }

  get sound() {
    return this._sound;
  }

  get retries() {
    return this._retries;
  }

  initialize() {
    this._load = new LoadModel();
  }

  // CTA
  initializeCtaModel() {
    this._cta = new CtaModel();
    this._cta.initialize();
  }

  destroyCtaModel() {
    this._cta.destroy();
    this._cta = null;
  }

  // HINT
  initializeHintModel() {
    this._hint = new HintModel();
    this._hint.initialize();
  }

  destroyHintModel() {
    this._hint.destroy();
    this._hint = null;
  }

  // LOAD
  initializeLoadModel() {
    this._load = new LoadModel();
    this._load.initialize();
  }

  destroyLoadModel() {
    this._load.destroy();
    this._load = null;
  }

  // TIMER
  initializeTimerModel() {
    this._timer = new TimerModel();
  }

  destroyTimerModel() {
    this._timer.destroy();
    this._timer = null;
  }

  // POWER UP
  initializePowerUpModel() {
    this._powerUp = new PowerUpModel();
    this._powerUp.initialize();
  }

  destroyPowerUpModel() {
    this._powerUp.destroy();
    this._powerUp = null;
  }

  // JOYSTICK
  initializeJoystickModel() {
    this._joystick = new JoystickModel();
    this._joystick.initialize();
  }

  destroyJoystickModel() {
    this._joystick.destroy();
    this._joystick = null;
  }

  // TUTORIAL
  initializeTutorialModel() {
    this._tutorial = new TutorialModel();
    this._tutorial.initialize();
  }

  destroyTutorialModel() {
    this._tutorial.destroy();
    this._tutorial = null;
  }

  // PERSISTENT_CTA
  initializePersistentCtaModel() {
    this._persistentCta = new PersistentCtaModel();
    this._persistentCta.initialize();
  }

  destroyPersistentCtaModel() {
    this._persistentCta.destroy();
    this._persistentCta = null;
  }

  // PLAYABLE INDICATOR
  initializePlayableIndicator() {
    this._playableIndicator = new IndicatorModel();
    this._playableIndicator.initialize();
  }

  destroyPlayableIndicatorModel() {
    this._playableIndicator.destroy();
    this._playableIndicator = null;
  }

  // SOUND;
  initializeSoundModel() {
    this._sound = new SoundModel();
    this._sound.initialize();
  }

  destroySoundModel() {
    this._sound.destroy();
    this._sound = null;
  }

  decreaseRetriesCount() {
    this._retries -= 1;
  }
}
