import { lego } from '@armathai/lego';
import { AdStatus } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { joystickModelGuard } from '../../guards/ad/joystick-model-guard';
import { persistentCtaModelGuard } from '../../guards/ad/persistent-cta-model-guard';
import { playableIndicatorGuard } from '../../guards/ad/playable-indicator-guard';
import { powerUpModelGuard } from '../../guards/ad/power-up-model-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { timerModelGuard } from '../../guards/timer/timer-model-guard';
import { store } from '../../models/store';
import { mapPlayableCommandsCommand } from '../game/map-playable-commands-command';
import { unmapPlayableCommandsCommand } from '../game/unmap-playable-commands-command';
import { destroyIndicatorModelCommand } from '../indicator/destroy-indicator-model-command';
import { initializeModelsCommand } from '../initialize-models-command';
import { shutdownModelsCommand } from '../shutdown-models-command';
import { resetPowerUpCommand } from '../snake/reset-power-up-command';
import { restartGameStateCommand } from '../states/restart-game-state-command';
import { destroyHintModelCommand } from './hint/destroy-hint-model-command';
import { destroyJoystickModelCommand } from './joystick/destroy-joystick-model-command';
import { destroyPersistentCtaModelCommand } from './pcta/destroy-persistent-cta-model-command';
import { destroyPowerUpModelCommand } from './power_up/destroy-power-up-model-command';
import { setAdStatusCommand } from './set-ad-status-command';
import { destroyTimerModelCommand } from './timer/destroy-timer-model-command';
import { stopTimerCommand } from './timer/stop-timer-command';
import { setTutorialSkipCommand } from './tutorial/set-tutorial-skip-command';

export function adStatusUpdateCommand(status) {
  switch (status) {
    case AdStatus.Game:
      lego.command
        //
        .execute(mapPlayableCommandsCommand, initializeModelsCommand);
      break;

    case AdStatus.Restart:
      lego.command
        //
        .execute(restartGameStateCommand);
      break;

    case AdStatus.PreCta:
      lego.command
        //
        .payload(store.game.boardModel.playerSnake.uuid)
        .execute(resetPowerUpCommand)

        .guard(tutorialModelGuard)
        .execute(setTutorialSkipCommand)

        .execute(unmapPlayableCommandsCommand)

        .guard(timerModelGuard)
        .execute(stopTimerCommand, destroyTimerModelCommand)

        .guard(joystickModelGuard)
        .execute(destroyJoystickModelCommand)

        .guard(powerUpModelGuard)
        .execute(destroyPowerUpModelCommand)

        .guard(hintModelGuard)
        .execute(destroyHintModelCommand)

        .guard(playableIndicatorGuard)
        .execute(destroyIndicatorModelCommand);
      break;

    case AdStatus.Cta:
      lego.command
        //
        .guard(persistentCtaModelGuard)
        .execute(destroyPersistentCtaModelCommand);
      break;

    case AdStatus.Retry:
      lego.command
        //
        .execute(shutdownModelsCommand)

        .payload(AdStatus.Game)
        .execute(setAdStatusCommand);
      break;
    default:
  }
}
